import { Component, OnInit } from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { UserService } from 'src/app/shared/services/user.service';
import { Title } from '@angular/platform-browser';
import { TranslateService } from '@ngx-translate/core';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Location } from '@angular/common';
import { OperationsService } from 'src/app/shared/services/operations.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-user-change-password',
  templateUrl: './user-change-password.component.html',
  styleUrls: ['./user-change-password.component.css']
})
export class UserChangePasswordComponent implements OnInit {

  pageName = 'ChangePassword';
  passwordStrength = 0;
  deniedChars: '';
  isLoading = false;
  responseError = '';
  initialMessage = false;

  myDataForm = new FormGroup({
    currentPassword: new FormControl('', Validators.required),
    newPassword: new FormControl('', [
      Validators.required,
      this.isNewPasswordInitialValues,
      this.isPasswordStrength.bind(this),
      this.isDeniedChars.bind(this),
      this.checkNewPasswordLength,
    ]),
    confirmNewPassword: new FormControl('', [
      Validators.required,
      this.isPasswordStrength.bind(this),
      this.isDeniedChars.bind(this),
      this.checkNewPasswordLength]),
    currentQuestion: new FormControl('', Validators.required),
    newQuestion: new FormControl('', [Validators.required, this.isNewQuestionInitialValues]),
    newAnswer: new FormControl('', Validators.required)
  });

  constructor(
    public config: ConfigService,
    private route: ActivatedRoute,
    private userService: UserService,
    private titleService: Title,
    private translate: TranslateService,
    private location: Location,
    private operations: OperationsService) { }

  ngOnInit(): void {
    this.titleService.setTitle(this.config.GetTranslationByPage(this.pageName, 'PageTitle'));

    this.translate.getTranslation(this.config.Culture);
    this.translate.use(this.config.Culture);
    this.myDataForm.controls.currentQuestion.disable();

    this.route.queryParams.subscribe({
      next: (res) => {
        if (res.page && res.page === 'select-operation') {
          this.initialMessage = true;
        }
      }
    });

    this.isLoading = true;
    this.operations.getPasswordRules(this.config.JWT.OperationID).subscribe(data => {
      this.passwordStrength = data.passwordStrength;
      this.deniedChars = data.denyChars;
      this.isLoading = false;
    });
  }

  onMyDataSubmit(): void {
    this.isLoading = true;
    this.userService.changePassword(
      this.myDataForm.controls.currentPassword.value,
      this.myDataForm.controls.newPassword.value,
      this.myDataForm.controls.confirmNewPassword.value,
      this.myDataForm.controls.newQuestion.value,
      this.myDataForm.controls.newAnswer.value,
      this.config.JWT.OperationID).subscribe(
        data => {
          this.navigateToLastRoute();
          this.isLoading = false;
        },
        error => {
          this.isLoading = false;
          this.responseError = error.detail;
        }
      );
  }

  isNewPasswordInitialValues(control: AbstractControl) {
    if (control.value === 'password') {
      return { validNewPasswordInitialValues: true };
    }
    return null;
  }

  checkNewPasswordLength(control: AbstractControl) {
    if (control.value.length <= 3) {
      return { validNewPasswordLength: true };
    }
    return null;
  }

  isNewQuestionInitialValues(control: AbstractControl) {
    if (control.value === 'pergunta') {
      return { validNewQuestionInitialValues: true };
    }
    return null;
  }

  isDeniedChars(control: AbstractControl) {
    if (control.value.includes(this.deniedChars)) {
      return { validDeniedChars: true };
    }
    return null;
  }

  checkIfStringHasrepeatedCharacteres(string: string): boolean {
    const text = string.split('');

    const hasrepeated = text.some(function (v, i, a) {
      return a.lastIndexOf(v) !== i;
    });

    return hasrepeated;
  }

  isPasswordStrength(control: AbstractControl) {
    const regexSpecialCharacteres = /(?:[.!@#$%^&*()-]){1}/g;
    const regexNumerals = /(?:[0-9]){1}/g;
    const regexCapital = /(?:[A-Z]){1}/g;

    switch (this.passwordStrength) {
      case 1:
        if (!regexSpecialCharacteres.test(control.value)) {
          return { validNewPasswordStrength: true };
        } else {
          return null;
        }
        break;
      case 2:
        if (
          !regexCapital.test(control.value) ||
          !regexNumerals.test(control.value) ||
          !regexSpecialCharacteres.test(control.value)
        ) {
          return { validNewPasswordStrength: true };
        } else {
          return null;
        }
        break;
      case 3:
        if (
          !regexCapital.test(control.value) ||
          !regexNumerals.test(control.value) ||
          !regexSpecialCharacteres.test(control.value) ||
          this.checkIfStringHasrepeatedCharacteres(control.value)
        ) {
          return { validNewPasswordStrength: true };
        } else {
          return null;
        }
      default:
        return null;
    }
  }

  navigateToLastRoute(): void {
    this.location.back();
  }

}
