import { DOCUMENT } from '@angular/common';
import { Title } from '@angular/platform-browser';
import { Component, OnDestroy, Inject } from '@angular/core';

import { ConfigService } from '../../../shared/services/config.service';
import { TranslateService } from '@ngx-translate/core';
import { UserService } from '../../../shared/services/user.service';
import { finalize } from 'rxjs/operators';
import { ReCaptchaV3Service } from 'ngx-captcha';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})

export class ForgotPasswordComponent implements OnDestroy {

  pageName = 'ForgotPassword';
  siteKey = '6LdpU5YUAAAAAGa1DHW5F_vC4vRwcPpEONc1Vp-7';
  action = 'forgotPassword';
  token: string;

  errors: string;
  isRequesting: boolean;
  isRecoverySuccessful = false;
  successMessage: string;
  username: string;

  constructor(private config: ConfigService, private userService: UserService, private reCaptchaV3Service: ReCaptchaV3Service,
              titleService: Title, public translate: TranslateService, @Inject(DOCUMENT) private document: any) {

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    titleService.setTitle(config.GetTranslationByPage('ForgotPassword', 'ForgotPasswordTitle'));

    this.generateToken();
   }

   ngOnDestroy(): void {

    const recaptcha = this.document.getElementsByClassName('grecaptcha-badge')[0];
    recaptcha.style.visibility = 'hidden';
  }

  generateToken() {
    this.reCaptchaV3Service.execute(this.siteKey, this.action, (result) => {
      this.token = result;
    });
  }

  recoverPassword({ value, valid }: { value: any, valid: boolean }) {

    this.isRecoverySuccessful = false;
    this.isRequesting = true;
    this.errors = '';

    this.generateToken();
    if (valid) {
        this.userService.recoverPassword(value.username, this.token).pipe(
          finalize(() => {
            this.finishRequest();
          }))
          .subscribe( result => {
            this.isRecoverySuccessful = true;
            this.successMessage = result.result;
          },
          (error) => this.errors = this.config.GetTranslationByPage(this.pageName, error));
      }
  }

  finishRequest() {
    const boxLogin = document.querySelector('.box-login');
    boxLogin.classList.remove('loading');
    this.isRequesting = false;
  }
}
