import { Component, OnInit} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { PhoneCallService } from 'src/app/shared/services/phone-calls.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-select-phone-channel',
  templateUrl: './select-phone-channel.component.html',
  styleUrls: ['./select-phone-channel.component.css']
})
export class SelectPhoneChannelComponent implements OnInit {

  pageName = 'SelectPhoneChannel';
  PhoneChannels;

  constructor(public config: ConfigService, public translate: TranslateService, private phoneCallService: PhoneCallService,
    private router: Router) {
    this.loadPhoneChannels(this.config.JWT.UserId, this.config.JWT.OperationID);
  }

  ngOnInit() {
  }

  closeModal() {
    const modal = document.getElementById('modal-select-phone-channel');
    modal.classList.remove('opened');
  }

  closeStatusModal() {
    const modal = document.getElementById('modal-status');
    modal.classList.remove('opened');
  }

  loadPhoneChannels(userId, operationId) {
    this.phoneCallService.getPhoneChannels(userId, operationId).subscribe(result => {
      if (result) {
        this.PhoneChannels = result.phoneChannels;
      }
    });
  }

  selectPhoneChannel(phoneChannel) {
    this.closeModal();
    this.closeStatusModal();
    this.config.PhoneChannel = phoneChannel;
    this.router.navigateByUrl('/relation-phone-call');
  }
}
