import { Component, OnInit, Input } from '@angular/core';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { CustomerContactService } from 'src/app/shared/services/customer-contact.service';
import { ConfigService } from 'src/app/shared/services/config.service';
import { Contact } from 'src/app/shared/models/contact.interface';
import { CustomersService } from 'src/app/shared/services/customers.service';
import { CustomerCompanyService } from 'src/app/shared/services/customer-company.service';
import { CustomerInfo } from 'src/app/shared/models/customer-info.interface';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'app-contact-quick-register',
  templateUrl: './contact-quick-register.component.html',
  styleUrls: ['./contact-quick-register.component.css']
})

export class ContactQuickRegisterComponent implements OnInit {

  pageName = 'ContactQuickRegister';
  errors;
  savingSuccessful = false;
  public validation;
  // Contact
  @Input()
  CustomerId;

  OperationId;
  PersonId;
  CompanyName;
  PersonName;
  Phone;
  Email;
  JobTitle;
  Department;
  Notes;
  Enabled = true;
  AccountablePerson = -1;
  accountablePeople;
  Family = -1;

  constructor(private config: ConfigService, private customerContactService: CustomerContactService,
    private customersService: CustomersService, private customerCompanyService: CustomerCompanyService) { }

  ngOnInit() {
    this.getContactRequiredFields();
  }

  getContactRequiredFields() {
    this.customerContactService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.validation = new ValidationService(result.requiredFields);
        this.loadAccountablePeople();
        this.loadCompany(this.CustomerId);
      }
    });
  }

  receiveMessage($event) {

    const customer: CustomerInfo = $event;
    this.PersonId = customer.Id;
    this.PersonName = customer.Name;

    this.validation.removeInvalid('PersonName');
  }

  loadCompany(customerId: number) {
    this.customerCompanyService.getCompany(customerId).subscribe(result => {
      const company = result.company;
      this.CompanyName = company.TradeName;
    });
  }

  saveContact({ value, valid }: { value: Contact, valid: boolean }) {
    this.errors = '';
    this.savingSuccessful = false;

    if (valid && this.validateForm(value)) {

      value.CustomerId = this.CustomerId;
      value.PersonId = this.PersonId;
      value.CreatedBy = this.config.JWT.UserId;
      value.OperationId = this.config.JWT.OperationID;

      this.customerContactService.save(value).subscribe((result: any) => {
        if (result) {
          this.savingSuccessful = true;
        }
      },
        (error) => {
          this.savingSuccessful = false;
          this.errors = this.config.GetTranslationByPage(this.pageName, error);
        });

      const element = document.getElementsByClassName('form-router-modal')[0];
      element.scrollTop = 0;
    }
  }

  closeModal() {
    this.errors = '';
    this.savingSuccessful = false;
    const modal = document.getElementById('modal-contact-quick-register');
    modal.classList.remove('opened');
  }

  loadAccountablePeople() {
    this.customersService.getAccountablePeople(this.config.JWT.OperationID).subscribe(result => {
      this.accountablePeople = result.accountablePeople;
    });
  }

  openSelectPerson() {
    const modal = document.getElementById('modal-select-client');
    modal.classList.add('opened');
  }

  validateField(fieldName, value) {
    if (!value) {
      this.validation.invalidFields.push(fieldName);
    }
  }

  validateForm(contact: Contact): boolean {

    const personValidation = contact.PersonName === this.config.GetTranslationByPage(this.pageName, 'ddoSelect') ? undefined : contact.PersonName;

    this.validateField('PersonName', personValidation);
    this.validateField('Phone', contact.Phone);
    this.validateField('Email', contact.Email);
    this.validateField('JobTitle', contact.JobTitle);
    this.validateField('Department', contact.Department);
    this.validateField('Notes', contact.Notes);

    return this.validation.invalidFields.length === 0;
  }
}
