import { Component, Input, EventEmitter, Output } from '@angular/core';
import { Column } from 'src/app/shared/models/column.interface';

@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css']
})
export class TableComponent {
  @Output() ordering: EventEmitter<any> = new EventEmitter();
  @Input() data: any;
  @Input() pageName: string;
  @Input() type: string; //criar enum
  @Input() set columns(value: Column[]) {
    this.displayedColumns = value;
    this.stringColumns = value.map(col => {
      return col.columnName
    });
  }
  get columns(): Column[] {
    return this.displayedColumns;
  }
  displayedColumns: Column[];
  stringColumns: string[] = [];

  hideColumns(column: Column) {
    let originalIndex;
    let currentIndex;

    this.displayedColumns.map((item, i) => {
      if (item.columnName === column.columnName) {
        originalIndex = i;
        item.initialVisible = !item.initialVisible;
      }
    })

    let result: string[] = []
    this.stringColumns.map((item, i) => {
      if (item === column.columnName) {
        currentIndex = i;
        result.push(item)
      }
    })
    console.log('original index', originalIndex);
    console.log('this.stringColumns.length', this.stringColumns.length);

    if (result?.length > 0) {
      this.stringColumns.splice(currentIndex, 1)
    } else {
      this.stringColumns.splice(originalIndex, 0, column.columnName)
    }

  }

  sortData(event) {
    this.ordering.emit(event);
  }
}
