import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { ConfigService } from './config.service';
import { catchError, map } from 'rxjs/operators';
import { BaseService } from './base.service';
import { CTILogin } from '../models/cti-login.interface';

@Injectable({ providedIn: 'root' })
export class OperationsService extends BaseService {

  constructor(private httpClient: HttpClient, private config: ConfigService) {
    super();
  }

  getLabels(operationId: number) {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/operations/${operationId}/labels`
    ).pipe(
      catchError(this.handleError)
    );
  }

  getLogo(operationId: number) {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/operations/${operationId}/logo`
    ).pipe(
      map((result) => {
        return result;
      }),
      catchError(this.handleError)
    );
  }

  listOperations(userName: string, enabledOnly?: boolean) {
    let params = new HttpParams().set('userName', userName);

    if (enabledOnly) {
      params = new HttpParams().set('userName', userName).set('enabledOnly', enabledOnly.toString());
    }

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/operations`, { params }
    ).pipe(
      catchError(this.handleError)
    );
  }

  selectOperation(operationId: number, ctiLogin?: CTILogin) {
    return this.httpClient.post<number>(
      `${this.config.BackendUri}/api/operations/${operationId}/select`, ctiLogin
    ).pipe(
      catchError(this.handleError)
    );
  }

  getPasswordRules(operationId: number) {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/operations/${operationId}/password-rules`
    ).pipe(catchError(this.handleError));
  }

  getCtiRules(operationId: number) {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/operations/${operationId}/cti-rules`
    ).pipe(catchError(this.handleError));
  }

  getReasons(operationId: number) {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/operations/${operationId}/reasons`
    ).pipe(catchError(this.handleError));
  }
}
