import { FormGroup } from '@angular/forms';
import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormControlBase } from 'src/app/shared/models/reactive-forms/form-control-base';
import { ReactiveFormsService } from 'src/app/shared/services/reactive-forms.service';
import { TextboxControl } from 'src/app/shared/models/reactive-forms/textbox-control';
import { TextareaControl } from 'src/app/shared/models/reactive-forms/textarea-control';
import { DropdownControl } from 'src/app/shared/models/reactive-forms/dropdown-control';
import { CalendarControl } from 'src/app/shared/models/reactive-forms/calendar-control';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { ConfigService } from 'src/app/shared/services/config.service';
import { CustomerPersonService } from 'src/app/shared/services/customer-person.service';
import { Person } from 'src/app/shared/models/person.interface';
import { CustomerInfo } from 'src/app/shared/models/customer-info.interface';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';
import { CustomerType } from 'src/app/shared/models/customer-type.enum';

@Component({
  selector: 'app-new-person',
  templateUrl: './new-person.component.html',
  styleUrls: ['./new-person.component.css']
})
export class NewPersonComponent implements OnInit {

  validator;
  controls: FormControlBase<any>[] = [];
  form: FormGroup;
  payLoad = '';
  pageName = 'CustomerPerson';

  errors;

  @Output() messageEvent = new EventEmitter<string>();

  constructor(private rfs: ReactiveFormsService, private config: ConfigService, private customerPersonService: CustomerPersonService,
    private util: UtilitiesService) {
    this.getPersonRequiredFields();
  }

  ngOnInit() {
    this.loadControls(this.validator.requiredFieldsList);
    this.form = this.rfs.toFormGroup(this.controls);
  }

  getPersonRequiredFields() {
    this.customerPersonService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
        if (result) {
          this.validator = new ValidationService(result.requiredFields);
        }
    });
  }

  savePerson({ value, valid }: { value: Person, valid: boolean }) {
    this.errors = '';

    this.validator.validateSave(CustomerType.Person, value);

    if (valid) {

      value.CreatedBy = this.config.JWT.UserId;
      value.OperationId = this.config.JWT.OperationID;
      value.CustomerId = -1;
      value.AccountablePerson = -1;

      this.customerPersonService.save(value).subscribe((result: any) => {
        if (result) {
          const customer = new CustomerInfo();
          customer.Id = result.person.CustomerId;
          customer.Name = this.getFullName(result.person);
          customer.Email = result.person.EmailAddress1;
          customer.Mobile = result.person.MobilePhone;
          customer.Type = 1;

          this.selectPerson(customer);
        }
      },
      (error) => {
        this.errors = this.config.GetTranslationByPage(this.pageName, error);
        const element = document.getElementById('panel-client-1');
        element.scrollTop = 0;
      });
    }
  }

  getFullName(person) {
    return this.util.getFullName(person.FirstName, person.MiddleName, person.LastName);
  }

  selectPerson(customer) {
    this.errors = '';
    this.form.reset();
    this.form.patchValue({ Gender : '-1'});
    this.validator.invalidFields = [];
    this.messageEvent.emit(customer);
  }

  getRequiredFieldsList(requiredFields): any {

    const fieldsList = [];
    const fieldsKeys = Object.keys(requiredFields);

    fieldsKeys.forEach(field => {
      if (requiredFields[field] === true) {
        fieldsList.push(field);
      }
    });

    return fieldsList;
  }

  loadControls(requiredFieldsList) {

    let controlOrder = 1;

    requiredFieldsList.forEach(field => {

      let control;

      const controlLabel = this.config.GetTranslationByPage(this.pageName, 'lbl' + field);

      switch (field) {
        case 'FirstName':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'MiddleName':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'LastName':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'Nickname':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'BirthDate':
            control = new CalendarControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'Email':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Email'
            });
            break;
        case 'Email2':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Email'
            });
            break;
        case 'TaxId':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'CPF'
            });
            break;
        case 'TaxId2':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'Phone':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Phone'
            });
            break;
        case 'Phone2':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Phone'
            });
            break;
        case 'Phone3':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Phone'
            });
            break;
        case 'Mobile':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Phone'
            });
            break;
        case 'Website':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'Facebook':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'PostalCode':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'PostalCode'
            });
            break;
        case 'AddressLine1':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'AddressLine2':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'AddressNumber':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'AddressNumberComp':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'City':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'State':
            control = new TextboxControl({
              key: field,
              label: controlLabel,
              required: true,
              order: controlOrder++,
              maskType: 'Text'
            });
            break;
        case 'Country':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'Gender':
          control = new DropdownControl({
            key: field,
            label: controlLabel,
            value: '-1',
            options: [
              { key: '-1', value: this.config.GetTranslationByPage(this.pageName, 'ddoSelect') },
              { key: 0, value: this.config.GetTranslationByPage(this.pageName, 'ddoMale') },
              { key: 1, value: this.config.GetTranslationByPage(this.pageName, 'ddoFemale') }
            ],
            order: controlOrder++
          });
          break;
        case 'AccountablePerson':
          control = new DropdownControl({
            key: field,
            label: controlLabel,
            value: '-1',
            options: [
              { key: '-1', value: this.config.GetTranslationByPage(this.pageName, 'ddoSelect') }
            ],
            order: controlOrder++
          });
          break;
        case 'Comments':
          control = new TextareaControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
      }

      if (control !== undefined) {
        this.controls.push(control);
      }
    });
  }
}
