import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';

import { Credentials } from '../../../shared/models/credentials.interface';
import { UserService } from '../../../shared/services/user.service';

import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from '../../../shared/services/config.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})

export class LoginComponent implements OnInit {

  errors: string;
  isRequesting: boolean;
  credentials: Credentials = { username: '', password: '', token: '', refreshToken: '' };
  pageName = 'Login';

  constructor(private config: ConfigService, private userService: UserService, private router: Router,
    public translate: TranslateService, private titleService: Title) { }

  ngOnInit(): void {
    this.titleService.setTitle('Login :: Orbium');

    const browserLang = this.translate.getBrowserLang();

    this.config.Culture = browserLang.match(/pt|es|en/) ? browserLang : 'pt-br';

    if (browserLang === 'pt') {
      this.config.Culture = 'pt-br';
    }

    // TODO: REMOVER QUANDO HOUVER TRADUÇÕES PARA OUTRAS LÍNGUAS.
    this.config.Culture = 'pt-br';

    this.translate.addLangs(['pt', 'es', 'en']);
    this.translate.setDefaultLang(this.config.Culture);
    this.translate.getTranslation(this.config.Culture);
    this.translate.use(this.config.Culture);
  }

  get translationObject() {
    const translation = JSON.parse(localStorage.getItem('TranslationObject'));
    return Object.keys(translation).length;
  }

  login({ value, valid }: { value: Credentials, valid: boolean }) {
    this.isRequesting = true;
    this.errors = '';
    if (valid) {
      this.userService.login(value.username, value.password).subscribe((result: boolean) => {
        if (result) {
          const roles = this.config.JWT.Roles;
          if (roles.includes('Desktop')) {
            this.router.navigate(['/select-operation']);
          } else {
            this.router.navigate(['/no-permission']);
          }
        }
      },
        (error) => {
          this.errors = this.config.GetTranslationByPage(this.pageName, error);
          this.finalizeLoading();
        });
    }
  }

  finalizeLoading(): void {
    const boxLogin = document.querySelector('.box-login');
    boxLogin.classList.remove('loading');
    this.isRequesting = false;
  }
}
