import { Router } from '@angular/router';
import { Component, OnInit, AfterViewChecked, ChangeDetectorRef } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { MatDialog } from '@angular/material/dialog';
import { interval, forkJoin } from 'rxjs';
import { exhaustMap } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';

import { UserService } from '../../../shared/services/user.service';
import { CTILogin } from '../../../shared/models/cti-login.interface';
import { ConfigService } from '../../../shared/services/config.service';
import { SessionsService } from '../../../shared/services/sessions.services';
import { FormGroup, FormControl, Validators, AbstractControl } from '@angular/forms';
import { OperationsService } from '../../../shared/services/operations.service';
import { ActiveSessionDialogComponent } from '../../../components/active-session-dialog/active-session-dialog.component';
import Operation from '../../../shared/models/operation';

@Component({
  selector: 'app-select-operation',
  templateUrl: './select-operation.component.html',
  styleUrls: ['./select-operation.component.css']
})
export class SelectOperationComponent implements OnInit, AfterViewChecked {

  loadingOperations = true;
  pageName = 'SelectOperation';
  errors: string;
  isRequesting: boolean;
  operations: Operation[] = [];
  selectedOperation: Operation;
  loadingCtiInfo = false;
  ctiLoginForm: FormGroup;

  constructor(
    public config: ConfigService,
    private userService: UserService,
    private operationsService: OperationsService,
    private sessionsServices: SessionsService,
    private router: Router,
    public translate: TranslateService,
    private titleService: Title,
    private dialog: MatDialog,
    private cdRef: ChangeDetectorRef
  ) { }

  ngAfterViewChecked() {
    this.cdRef.detectChanges();
  }

  ngOnInit() {
    this.translate.getTranslation(this.config.Culture);
    this.translate.use(this.config.Culture);
    this.titleService.setTitle(this.config.GetTranslationByPage(this.pageName, 'SelectOperationTitle'));
    this.loadAllEnabledOperationsByUser(this.config.JWT.unique_name);
  }

  loadAllEnabledOperationsByUser(username: string) {
    this.operationsService.listOperations(username, true).subscribe((result: Operation[]) => {
      if (result.length <= 0) {
        this.router.navigate(['/no-permission']);
        return;
      }
      this.operations = [...result];
      this.selectedOperation = result[0];
      this.ctiLoginForm = new FormGroup({
        operations: new FormControl(''),
        device: new FormControl(''),
        login: new FormControl(''),
        password: new FormControl(''),
      });
      this.loadFormValidators();
      this.ctiLoginForm.controls.operations.setValue(result[0]);
      this.getCtiInfo(this.config.JWT.unique_name, result[0].operationId);
      if (result.length === 1 && !result[0].ctiEnabled) {
        this.ctiLoginForm.controls.operations.setValue({
          operationId: result[0].operationId,
          operationName: result[0].name
        });
        this.trySelectOperation();
      }
      this.loadingOperations = false;
    });
  }

  loadFormValidators() {
    if (this.selectedOperation.ctiEnabled) {
      this.ctiLoginForm.clearValidators();
      const regex = new RegExp(this.selectedOperation.ctiLoginRegEx);
      if (this.selectedOperation.ctiDeviceJustNumbers) {
        this.ctiLoginForm.controls['device'].setValidators(Validators.pattern(regex));
      }
      if (this.selectedOperation.ctiLoginRegEx !== '') {
        this.ctiLoginForm.controls['login'].setValidators(Validators.pattern(regex));
      }
      if (this.selectedOperation.ctiLoginRequireDevice) {
        this.ctiLoginForm.setValidators(this.loginRequiredDevice.bind(this));
      }
    }
  }

  trySelectOperation(): void {
    this.errors = '';
    this.isRequesting = true;

    forkJoin({
      checkPassword: this.userService.checkChangePassword(
        this.config.JWT.unique_name,
        this.ctiLoginForm.controls.operations.value.operationId
      ),
      getState: this.sessionsServices.getCurrentStateByUserName(this.config.JWT.unique_name)
    }).subscribe({
      next: (res: any) => {
        if (res.checkPassword.message === true) {
          this.router.navigate(['/user-change-password'], { queryParams: { page: 'select-operation' } });
          return;
        } else {
          if (res.getState && this.isLogged(res.getState.state)) {
            this.dialog.open(ActiveSessionDialogComponent).afterClosed().subscribe(
              (dialogResult) => {
                if (dialogResult) {
                  this.SelectOperation({
                    ctiDevice: this.ctiLoginForm.controls.device.value,
                    ctiLogin: this.ctiLoginForm.controls.login.value,
                    ctiPassword: this.ctiLoginForm.controls.password.value
                  });
                }
              }
            );
          } else {
            this.SelectOperation({
              ctiDevice: this.ctiLoginForm.controls.device.value,
              ctiLogin: this.ctiLoginForm.controls.login.value,
              ctiPassword: this.ctiLoginForm.controls.password.value
            });
          }
        }
      },
      error: (err) => {
        this.finalizeLoading();
        this.errors = this.config.GetTranslationByPage(this.pageName, err);
      }
    });
  }

  SelectOperation(ctiLoginData?: CTILogin): void {
    this.operationsService.selectOperation(this.selectedOperation.operationId, ctiLoginData)
      .subscribe({
        next: (res: any) => {
          this.config.JWT = res.token;
          this.sessionChecker(res.userId);
        },
        error: (err) => {
          this.finalizeLoading();
          this.errors = this.config.GetTranslationByPage(this.pageName, err);
        }
      }
      );
  }

  sessionChecker(userId: number): void {
    let countChecker = 0;
    const subscription = interval(3000).pipe(
      exhaustMap(
        (countEstimated: number) => {
          countChecker = countEstimated;
          return this.sessionsServices.getCurrentStateByUserId(userId);
        }
      )
    ).subscribe({
      next: (response) => {
        if (response && !this.isNotLogged(response.state)) {
          subscription.unsubscribe();
          this.finalizeLoading();
          if (this.isLogged(response.state)) {
            this.config.JWT.UserId = userId;
            this.sessionsServices.getChannelLicenses(userId).subscribe();
            this.router.navigate(['/dashboard']);
          } else {
            this.errors = this.config.GetTranslationByPage(this.pageName, 'CtiLoginFailure');
          }
          return;
        }

        if (countChecker >= this.config.CtiMaxTimeout) {
          subscription.unsubscribe();
          this.errors = this.config.GetTranslationByPage(this.pageName, 'CtiTimeoutFailure');
          this.finalizeLoading();
        }
      },
      error: (err) => {
        this.errors = this.config.GetTranslationByPage(this.pageName, err);
        this.finalizeLoading();
      }
    });
  }

  isLogged(state: string) {
    return state === 'LOGGED-IN';
  }

  isNotLogged(state: string) {
    return state === 'NOT-LOGGED-IN';
  }

  finalizeLoading() {
    const boxLogin = document.querySelector('.box-login');
    boxLogin.classList.remove('loading');
    this.isRequesting = false;
  }

  numbersCtiDevice(control: AbstractControl) {
    const regexNumbers = /(^[0-9]+$|^$)/;
    if (!regexNumbers.test(control.value)) {
      return { ctiDeviceNumbers: true };
    } else {
      return null;
    }
  }

  loginRequiredDevice(formGroup: FormGroup) {
    if (
      (formGroup.get('login').value !== '' && formGroup.get('device').value === '') ||
      (formGroup.get('device').value !== '' && formGroup.get('login').value === '')) {
      return { loginRequiredDevice: true };
    } else {
      return null;
    }
  }

  changeOperation(): void {
    const operationId = this.ctiLoginForm.controls.operations.value.operationId;
    const selectedOperation = this.operations.find(item => item.operationId === operationId);
    this.selectedOperation = selectedOperation;
    this.loadFormValidators();
    if (operationId === 0) {
      return;
    }
    this.getCtiInfo(this.config.JWT.unique_name, operationId);
  }

  getCtiInfo(userName: string, operationId: number): void {
    if (this.selectedOperation.ctiClearLogin) {
      this.clearInputs();
      return;
    }
    this.loadingCtiInfo = true;
    this.disableInputs();
    this.userService.getCtiInfo(userName, operationId).subscribe({
      next: (res: any) => {
        this.ctiLoginForm.controls.device.setValue(res.ctiDevice);
        this.ctiLoginForm.controls.login.setValue(res.ctiLogin);
        this.ctiLoginForm.controls.password.setValue(res.ctiPassword);
        this.loadingCtiInfo = false;
        this.enableInputs();
      },
      error: (err) => {
        this.loadingCtiInfo = false;
        this.enableInputs();
        this.errors = this.config.GetTranslationByPage(this.pageName, err);
      }
    });
  }

  disableInputs() {
    this.ctiLoginForm.controls['device'].disable();
    this.ctiLoginForm.controls['login'].disable();
    this.ctiLoginForm.controls['password'].disable();
  }

  enableInputs() {
    this.ctiLoginForm.controls['device'].enable();
    this.ctiLoginForm.controls['login'].enable();
    this.ctiLoginForm.controls['password'].enable();
  }

  clearInputs() {
    this.ctiLoginForm.controls['device'].setValue('');
    this.ctiLoginForm.controls['login'].setValue('');
    this.ctiLoginForm.controls['password'].setValue('');
  }

  showSpecialCharacterError() {
    const regexDenySpecialChar = /(^[^"ç\^=/;.\\]*$)/;
    return this.ctiLoginForm.get('login').errors?.pattern &&
      this.ctiLoginForm.get('login').dirty &&
      this.ctiLoginForm.get('login').errors.pattern.requiredPattern === String(regexDenySpecialChar);
  }

  showNumberError() {
    const regexOnlyNumber = /^[0-9]+$/;
    return this.ctiLoginForm.get('login').errors?.pattern &&
      this.ctiLoginForm.get('login').dirty &&
      this.ctiLoginForm.get('login').errors.pattern.requiredPattern === String(regexOnlyNumber);
  }
}
