import { Component } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ConfigService } from '../../shared/services/config.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-articles',
  templateUrl: './articles.component.html',
  styleUrls: ['./articles.component.css']
})
export class ArticlesComponent {

  pageName = 'Articles';

  constructor(config: ConfigService, public translate: TranslateService, titleService: Title) {

    const body = document.getElementsByTagName('body')[0];
    body.className = 'articles-list-page';

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    titleService.setTitle(config.GetTranslationByPage(this.pageName, 'ArticlesTitle'));
  }
}
