import { Router } from '@angular/router';
import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from '../../shared/services/config.service';

@Component({
  selector: 'app-session-timeout-dialog.component',
  templateUrl: './session-timeout-dialog.component.html',
  styleUrls: ['./session-timeout-dialog.component.css']
})

export class SessionTimeoutDialogComponent {

  pageName = 'SessionTimeoutDialog';

  constructor( private router: Router, private config: ConfigService, public translate: TranslateService,
               private dialogRef: MatDialogRef<SessionTimeoutDialogComponent>) {

    translate.getTranslation(this.config.Culture);
    translate.use(this.config.Culture);
  }

  public close() {
    this.router.navigate(['/login']);
    this.dialogRef.close();
}

  public continue() {
      this.dialogRef.close(true);
  }
}
