import { FormControlBase } from './form-control-base';

export class TextboxControl extends FormControlBase<string> {
  controlType = 'textbox';
  type: string;
  maskType: string;

  constructor(options: {} = {}) {
    super(options);
    this.type = options['type'] || '';
    this.maskType = options['maskType'] || '';
  }
}
