import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.css']
})
export class PaginationComponent implements OnInit {

  @Output() pageChanging: EventEmitter<number> = new EventEmitter();
  @Input() set pages(value: number) {
    this.pagesArray = [...Array(Math.ceil(value / 25)).keys()]
  }
  get pages(): number {
    return this._pages;
  }
  pageNumber = 0;
  _pages: number;
  pagesArray: number[];

  constructor() { }

  ngOnInit(): void {
  }

  changePage(page: number) {
    this.pageNumber = page;
    this.pageChanging.emit(this.pageNumber + 1);
  }

  previousPage() {
    this.pageNumber--
    this.pageChanging.emit(this.pageNumber + 1);
  }

  nextPage() {
    this.pageNumber++
    this.pageChanging.emit(this.pageNumber + 1);
  }

}
