import { Component, OnInit } from '@angular/core';
import { EChartOption } from 'echarts';
import { CasesService } from 'src/app/shared/services/cases.service';
import { ConfigService } from 'src/app/shared/services/config.service';

@Component({
  selector: 'app-echart-pizza',
  templateUrl: './echart-pizza.component.html',
  styleUrls: ['./echart-pizza.component.css']
})
export class EchartPizzaComponent implements OnInit {

  options: EChartOption;
  chartInstance: any;

  constructor(private casesService: CasesService, private configService: ConfigService) { }

  ngOnInit(): void {
    this.loadOptions();
  }

  loadOptions() {
    this.casesService.getSummaryInfo(this.configService.JWT.UserId)
      .subscribe(result => {
        const data = [
          { value: result.opened, name: 'Abertos', itemStyle: { color: '#CC00FF' } },
          { value: result.due, name: 'Atrasados', itemStyle: { color: '#F05050' } },
          { value: result.closed, name: 'Finalizados', itemStyle: { color: '#30c27c' } },
        ];
        this.options = {
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b} : {c} ({d}%)',
          },
          legend: {
            type: 'scroll',
            orient: 'horizontal',
            right: 50,
            top: 350,
            bottom: 10,
          },
          series: [
            {
              selectedMode: true,
              label: {
                formatter: '{b}: {c}',
                show: true,
                position: 'outside',
                fontSize: 9,
                verticalAlign: 'bottom',
                labelLine: true
              },
              name: 'Casos',
              type: 'pie',
              radius: '50%',
              data: data,
              roseType: 'radius',
              animationType: 'scale',
              animationEasing: 'elasticOut',
              animationDelay: () => Math.random() * 200,
            },
          ],
        };
      });
  }
}
