import { Component, OnInit, Input } from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { UserService } from 'src/app/shared/services/user.service';

@Component({
  selector: 'app-performance-panel',
  templateUrl: './performance-panel.component.html',
  styleUrls: ['./performance-panel.component.css']
})
export class PerformancePanelComponent implements OnInit {

  pageName = 'Dashboard';
  today: number = Date.now();
  timeInterval = 'today';
  relationType = 0;
  relationTypes;
  menuOptions = [ 'channels', 'news', 'tasks', 'interchat'];
  selectedOption = 'channels';

  constructor(private config: ConfigService, private userService: UserService) { }

  ngOnInit(): void {
    this.getRelationTypes();
  }

  changeTimeInterval(timePeriod) {
    this.timeInterval = timePeriod;
  }

  getRelationTypes() {
    this.userService.getRelationPermissions(this.config.JWT.UserId).subscribe(result => {
      if (result) {
        this.relationTypes = result;
      }
    });
  }

  changeRelationType(relType) {
    this.relationType = relType;
    this.selectedOption = 'channels';
  }

  selectMenuOption(option) {
    this.relationType = -1;
    this.selectedOption = option;
  }
}
