import { FormGroup } from '@angular/forms';
import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormControlBase } from 'src/app/shared/models/reactive-forms/form-control-base';
import { ReactiveFormsService } from 'src/app/shared/services/reactive-forms.service';
import { TextboxControl } from 'src/app/shared/models/reactive-forms/textbox-control';
import { TextareaControl } from 'src/app/shared/models/reactive-forms/textarea-control';
import { DropdownControl } from 'src/app/shared/models/reactive-forms/dropdown-control';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { ConfigService } from 'src/app/shared/services/config.service';
import { Company } from 'src/app/shared/models/company.interface';
import { CustomerCompanyService } from 'src/app/shared/services/customer-company.service';
import { CustomerInfo } from 'src/app/shared/models/customer-info.interface';
import { CustomerType } from 'src/app/shared/models/customer-type.enum';

@Component({
  selector: 'app-new-company',
  templateUrl: './new-company.component.html',
  styleUrls: ['./new-company.component.css']
})
export class NewCompanyComponent implements OnInit {

  validator;
  controls: FormControlBase<any>[] = [];
  form: FormGroup;
  payLoad = '';
  pageName = 'CustomerCompany';

  errors;

  @Output() messageEvent = new EventEmitter<string>();

  constructor(private rfs: ReactiveFormsService, private config: ConfigService, private customerCompanyService: CustomerCompanyService) {
    this.getCompanyRequiredFields();
  }

  ngOnInit() {
    this.loadControls(this.validator.requiredFieldsList);
    this.form = this.rfs.toFormGroup(this.controls);
  }

  getCompanyRequiredFields() {
    this.customerCompanyService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.validator = new ValidationService(result.requiredFields);
      }
    });
  }

  saveCompany({ value, valid }: { value: Company, valid: boolean }) {

    this.errors = '';
    this.validator.validateSave(CustomerType.Company, value);

    if (valid) {

      value.CreatedBy = this.config.JWT.UserId;
      value.OperationId = this.config.JWT.OperationID;
      value.CustomerId = -1;
      value.AccountablePerson = -1;

      this.customerCompanyService.save(value).subscribe((result: any) => {
        if (result) {
          const customer = new CustomerInfo();
          customer.Id = result.company.CustomerId;
          customer.Name = result.company.FullName;
          customer.Phone = result.company.Telephone1;
          customer.Email = result.company.EmailAddress1;
          customer.Type = 2;

          this.selectCompany(customer);
        }
      },
        (error) => {
          this.errors = this.config.GetTranslationByPage(this.pageName, error);
          const element = document.getElementById('panel-client-2');
          element.scrollTop = 0;
        });
    }
  }

  selectCompany(customer) {
    this.errors = '';
    this.form.reset();
    this.validator.invalidFields = [];
    this.messageEvent.emit(customer);
  }

  loadControls(requiredFieldsList) {

    let controlOrder = 1;

    requiredFieldsList.forEach(field => {

      let control;

      const controlLabel = this.config.GetTranslationByPage(this.pageName, 'lbl' + field);

      switch (field) {
        case 'Name':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'TradeName':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'Industry':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AnnualRevenue':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Money'
          });
          break;
        case 'Employees':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Number'
          });
          break;
        case 'Email':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Email'
          });
          break;
        case 'Email2':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Email'
          });
          break;
        case 'TaxId':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'CNPJ'
          });
          break;
        case 'TaxId2':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'TaxId3':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'Phone':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Phone'
          });
          break;
        case 'Phone2':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Phone'
          });
          break;
        case 'Phone3':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Phone'
          });
          break;
        case 'Website':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'Facebook':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'PostalCode':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'PostalCode'
          });
          break;
        case 'AddressLine1':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AddressLine2':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AddressNumber':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AddressNumberComp':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'City':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'State':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'Country':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AccountablePerson':
          control = new DropdownControl({
            key: field,
            label: controlLabel,
            value: '-1',
            options: [
              { key: '-1', value: this.config.GetTranslationByPage(this.pageName, 'ddoSelect') }
            ],
            order: controlOrder++
          });
          break;
        case 'Comments':
          control = new TextareaControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
      }

      if (control !== undefined) {
        this.controls.push(control);
      }
    });
  }

}
