import { Component, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { FormControlBase } from 'src/app/shared/models/reactive-forms/form-control-base';
import { ConfigService } from 'src/app/shared/services/config.service';
import { CustomersService } from 'src/app/shared/services/customers.service';

@Component({
  selector: 'app-reactive-control',
  templateUrl: './reactive-control.component.html'
})
export class ReactiveControlComponent {
  @Input() validator;
  @Input() control: FormControlBase<any>;
  @Input() form: FormGroup;
  @Input() customerType: string;
  get isValid() { return this.form.controls[this.control.key].valid; }
  pageName = 'Customer';

  constructor(config: ConfigService, private customersService: CustomersService) { }

  searchPostalCode() {
    this.customersService.searchBrazilianPostalCode(this.form.value.PostalCode).subscribe(result => {

      this.form.patchValue( {
        AddressLine1: result.address.AddressLine1,
        AddressLine2: result.address.AddressLine2,
        City: result.address.City,
        State: result.address.State,
        Country: result.address.Country,
      });
    });
  }

  setDate(date) {
    this.form.patchValue( {
      BirthDate: date
    });
  }
}
