import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { TranslateLoader } from '@ngx-translate/core';
import { Observable } from 'rxjs';
import { BaseService } from './base.service';
import { ConfigService } from './config.service';
import { catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class OrbiumTranslateLoader extends BaseService implements TranslateLoader {

  constructor(private httpClient: HttpClient) {
    super();
  }

  getTranslation(lang: string): Observable<any> {

      return new Observable(
        observer => {

          const translationSession = JSON.parse(localStorage.getItem('TranslationObject'));

          if (translationSession === null) {

            const config = new ConfigService();

            this.httpClient.get('assets/config/config.json')
            .subscribe((result: any) => {
              this.httpClient.get<any>(`${result.config.BackendUri}/api/globalization/${config.InterfaceName}/${lang}`)
              .subscribe((response: Response) => {
                localStorage.setItem('TranslationObject', JSON.stringify(response));
              }),
              catchError(this.handleError);
            });

            const localUri = `assets/i18n/${lang}.json`;

            this.httpClient.get(localUri)
              .subscribe((result: any) => {
                observer.next(result);
                observer.complete();
              }),
              catchError(this.handleError);

          } else {
            observer.next(translationSession);
            observer.complete();
          }
        }
      );
    }
}
