import { Component, Input, EventEmitter, Output } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';

import { ConfigService } from 'src/app/shared/services/config.service';
import { CustomerFilter } from 'src/app/shared/models/customer-filter.enum';
import { CustomersService } from 'src/app/shared/services/customers.service';
import { CustomerSearch } from 'src/app/shared/models/customer-search.interface';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { CustomerPersonService } from 'src/app/shared/services/customer-person.service';
import { CustomerCompanyService } from 'src/app/shared/services/customer-company.service';

@Component({
  selector: 'app-search-customer',
  templateUrl: './search-customer.component.html',
  styleUrls: ['./search-customer.component.css']
})
export class SearchCustomerComponent {

  @Input()
  isHidden = true;
  isLoading = false;

  pageName = 'SearchCustomer';
  errors: string;
  SearchValue = '';
  Customer;
  customers;
  personValidation;
  companyValidation;

  @Output()
  messageEvent = new EventEmitter<any>();

  @Output()
  CustomerFilter: CustomerFilter;

  constructor(
    public config: ConfigService,
    public translate: TranslateService,
    private customersService: CustomersService,
    private customerPersonService: CustomerPersonService,
    private customerCompanyService: CustomerCompanyService, ) {
    this.getPersonRequiredFields();
    this.getCompanyRequiredFields();
  }

  getPersonRequiredFields() {
    this.customerPersonService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.personValidation = new ValidationService(result.requiredField);
      }
    });
  }

  getCompanyRequiredFields() {
    this.customerCompanyService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.companyValidation = new ValidationService(result.requiredFields);
      }
    });
  }

  doSearch() {
    this.isLoading = true;
    const params = new CustomerSearch();
    params.UserId = this.config.JWT.UserId;
    params.OperationId = this.config.JWT.OperationID;
    params.SearchValue = this.SearchValue;
    params.SearchTypeId = 'name';
    params.SortDirection = 'asc';
    params.CustomerFilter = this.CustomerFilter;

    this.customers = undefined;

    this.customersService.searchByName(params).pipe(
      finalize(() => {
        this.finalizeLoading();
      })).subscribe(result => {
        if (result) {
          this.customers = result.searchResult;
        }
      },
        error => {
          this.errors = this.config.GetTranslationByPage(this.pageName, error);
          this.finalizeLoading();
        }
      );
  }

  finalizeLoading() {
    const boxLogin = document.querySelector('.clients-list-results');
    boxLogin.classList.remove('loading');
    this.isLoading = false;
  }

  selectCustomer(customer) {
    this.messageEvent.emit(customer);
  }

  openSelectContact(customer) {
    this.Customer = customer;
    const modal = document.getElementById('modal-select-contact');
    modal.classList.add('opened');
  }

}
