import { Component, Output, EventEmitter, Input } from '@angular/core';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { ConfigService } from 'src/app/shared/services/config.service';

@Component({
  selector: 'app-edit-notes',
  templateUrl: './edit-notes.component.html',
  styleUrls: ['./edit-notes.component.css']
})
export class EditNotesComponent {

  pageName = 'EditNotes';
  validation;

  @Input()
  Notes;

  @Output()
  messageEvent = new EventEmitter<string>();

  constructor(config: ConfigService) {
    this.validation = new ValidationService();
    this.validation.requiredFieldsList.push('Notes');
  }

  closeModal() {
    const modal = document.getElementById('modal-edit-notes');
    modal.classList.remove('opened');
  }

  saveNotes({value, valid}: { value: any, valid: boolean}) {

    if (valid && this.validation.customValidateSave(value)) {
      this.messageEvent.emit(value.Notes);
      this.closeModal();
    }
  }

}
