import { Title } from '@angular/platform-browser';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { HistoryCollection } from 'src/app/shared/models/history-collection.interface';
import { CustomerContactService } from 'src/app/shared/services/customer-contact.service';
import { CustomersService } from 'src/app/shared/services/customers.service';
import { CustomerPersonService } from 'src/app/shared/services/customer-person.service';
import { CustomerCompanyService } from 'src/app/shared/services/customer-company.service';

@Component({
  selector: 'app-contact-view',
  templateUrl: './contact-view.component.html',
  styleUrls: ['./contact-view.component.css']
})

export class ContactViewComponent implements OnInit {

  pageName = 'ContactView';
  validation;
  companyValidation;
  history = new Array();
  historiesCollection: HistoryCollection[];
  pageIndex = 0;
  Period = 30;
  periods = [30, 60, 90];
  filter = 'all';

  // Company Data
  CustomerId = -1;
  CompanyName: string;
  CompanyTaxId: string;
  TradeName: string;

  // Contact Data
  ContactId;
  PersonName: string;
  PersonTaxId: string;
  Phone: string;
  Email: string;
  Department: string;
  JobTitle: string;

  constructor(private router: Router, private config: ConfigService, public util: UtilitiesService, public translate: TranslateService,
    titleService: Title, private activatedRoute: ActivatedRoute, private customersService: CustomersService,
    private customerContactService: CustomerContactService, private customerPersonService: CustomerPersonService,
    private customerCompanyService: CustomerCompanyService) {

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    titleService.setTitle(config.GetTranslationByPage(this.pageName, 'ContactViewTitle'));

    this.getPersonRequiredFields();
    this.getCompanyRequiredFields();

    util.changeBody('clients-list-page');
  }

  ngOnInit() {

    if (this.router.url.includes('customer')) {
      const clientLink = document.querySelector('.clients-link');
      clientLink.classList.add('active');
    }

    this.activatedRoute.queryParams.subscribe((params) => {
      if (params['contactId']) {
        this.ContactId = +params['contactId'];
        this.loadContact(this.ContactId);
        this.loadHistory();
      }
    });
  }

  getPersonRequiredFields() {
    this.customerPersonService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.validation = new ValidationService(result.requiredFields);
      }
    });
  }

  getCompanyRequiredFields() {
    this.customerCompanyService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.companyValidation = new ValidationService(result.requiredFields);
      }
    });
  }

  loadContact(contactId: number) {
    this.customerContactService.getContact(contactId).subscribe(result => {
      const contact = result.contact;

      // Contact
      this.CustomerId = contact.CustomerId;
      this.CompanyName = contact.CompanyName;
      this.CompanyTaxId = contact.CompanyTaxId;
      this.TradeName = contact.TradeName;
      this.PersonName = contact.PersonName;
      this.PersonTaxId = contact.PersonTaxId;
      this.Phone = contact.Phone;
      this.Email = contact.Email;
      this.Department = contact.Department;
      this.JobTitle = contact.JobTitle;

    });
  }

  loadHistory() {

    this.customersService.getHistory(this.CustomerId, this.ContactId, this.config.JWT.UserId, this.Period,
      this.pageIndex, this.filter).subscribe(result => {

        if (result.history.length !== 0) {
          result.history.forEach(history => {
            this.history.push(history);
          });
          this.buildHistoryCollection(this.history);
        }
      });
  }

  buildHistoryCollection(history) {
    let dates = history.map(h => this.util.convertMonthYear(h.Date, this.config.Culture));
    dates = dates.filter((item, index) => dates.indexOf(item) === index);

    this.historiesCollection = new Array();

    dates.forEach(date => {
      const histories = history.filter(f => this.util.convertMonthYear(f.Date, this.config.Culture) === date);
      const historyCollection = new HistoryCollection();
      historyCollection.MonthYear = date;
      historyCollection.Histories = histories;

      this.historiesCollection.push(historyCollection);
    });
  }

  getFormattedDate(date: Date): string {
    return this.util.getFormattedDate(date, this.config.GetTranslationByPage(this.pageName, 'lblAt'));
  }

  loadMoreHistory() {
    this.pageIndex++;
    this.loadHistory();
  }

  changePeriod() {
    this.pageIndex = 0;
    this.history = new Array();
    this.loadHistory();
  }

  changeFilter(filter) {
    this.filter = filter;
    this.changePeriod();
  }
}
