import { Title } from '@angular/platform-browser';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { CustomerCompanyService } from 'src/app/shared/services/customer-company.service';
import { CustomerContactService } from 'src/app/shared/services/customer-contact.service';
import { HistoryCollection } from 'src/app/shared/models/history-collection.interface';
import { CustomersService } from 'src/app/shared/services/customers.service';

@Component({
  selector: 'app-company-view',
  templateUrl: './company-view.component.html',
  styleUrls: ['./company-view.component.css']
})

export class CompanyViewComponent implements OnInit {

  pageName = 'CompanyView';
  fullName;
  fullAddress;
  age: number;
  cityState: string;
  birthdayDate;
  formattedBirthDate;
  genderText;
  isBirthdayDate: boolean;
  validation;
  contacts;
  history = [];
  historiesCollection: HistoryCollection[];
  pageIndex = 0;
  Period = 30;
  periods = [30, 60, 90];
  filter = 'all';

  // Company Data
  CustomerId = -1;
  ContactId = 0;
  Name: string;
  TradeName: string;
  TaxId: string;
  TaxId2: string;
  TaxId3: string;
  Email: string;
  Email2: string;
  Phone: string;
  Phone2: string;
  Phone3: string;
  Employees: number;
  AnnualRevenue: number;
  Industry: string;
  Website: string;
  Facebook: string;
  Twitter: string;
  Comment: string;
  AccountablePerson = -1;
  Family = -1;

  // Address
  PostalCode: string;
  AddressLine1: string;
  AddressLine2: string;
  AddressNumber: string;
  AddressNumberComp: string;
  City: string;
  State: string;
  Country: string;

  constructor(private router: Router, private config: ConfigService, public util: UtilitiesService, public translate: TranslateService,
    titleService: Title, private activatedRoute: ActivatedRoute, private customerCompanyService: CustomerCompanyService,
    private customerContactService: CustomerContactService, private customersService: CustomersService) {

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    titleService.setTitle(config.GetTranslationByPage(this.pageName, 'CompanyViewTitle'));

    this.getCompanyRequiredFields();

    util.changeBody('clients-list-page');
  }

  ngOnInit() {

    if (this.router.url.includes('customer')) {
      const clientLink = document.querySelector('.clients-link');
      clientLink.classList.add('active');
    }

    this.activatedRoute.queryParams.subscribe((params) => {
      if (params['customerId']) {
        this.CustomerId = +params['customerId'];

        if (params['contactId']) {
          this.ContactId = +params['contactId'];
        }

        this.loadCompany(this.CustomerId);
        this.loadContacts(this.CustomerId);
        this.loadHistory();
      }
    });
  }

  getCompanyRequiredFields() {
    this.customerCompanyService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.validation = new ValidationService(result.requiredFields);
      }
    });
  }

  getFormattedBirthDate(): string {
    return this.birthdayDate.toLocaleDateString();
  }

  getAge(): number {

    const timeDiff = Math.abs(Date.now() - this.birthdayDate.getTime());
    const age = Math.floor((timeDiff / (1000 * 3600 * 24)) / 365.25);

    return age;
  }

  getFullAddress(): string {

    let fullAdress = this.AddressLine1;

    if (this.AddressNumber !== null && this.AddressNumber !== '') {
      fullAdress += ', ' + this.AddressNumber;
    }

    return fullAdress;
  }

  getCityState(): string {
    let cityState = this.City;

    if (this.State !== '') {
      cityState += '/' + this.State;
    }

    return cityState;
  }

  loadCompany(customerId: number) {
    this.customerCompanyService.getCompany(customerId).subscribe(result => {
      const company = result.company;

      // Company Data
      this.Name = company.Name;
      this.TradeName = company.TradeName;
      this.TaxId = company.TaxId;
      this.TaxId2 = company.TaxId2;
      this.TaxId3 = company.TaxId3;
      this.Email = company.Email;
      this.Email2 = company.Email2;
      this.Phone = company.Phone;
      this.Phone2 = company.Phone2;
      this.Phone3 = company.Phone3;
      this.Industry = company.Industry;
      this.Employees = company.Employees;
      this.AnnualRevenue = company.AnnualRevenue;
      this.Website = company.Website;
      this.Facebook = company.Facebook;
      this.Twitter = company.Twitter;
      this.Comment = company.Comment;
      this.AccountablePerson = company.AccountablePerson;

      // Address
      this.PostalCode = company.PostalCode;
      this.AddressLine1 = company.AddressLine1;
      this.AddressLine2 = company.AddressLine2;
      this.AddressNumber = company.AddressNumber;
      this.AddressNumberComp = company.AddressNumberComp;
      this.City = company.City;
      this.State = company.State;
      this.Country = company.Country;
      this.cityState = this.getCityState();
      this.fullAddress = this.getFullAddress();
    });
  }

  loadContacts(customerId: number) {
    this.customerContactService.getContacts(customerId).subscribe(result => {
      this.contacts = result.contacts;
    });
  }

  loadHistory() {

    this.customersService.getHistory(this.CustomerId, this.ContactId, this.config.JWT.UserId, this.Period,
      this.pageIndex, this.filter).subscribe(result => {

        if (result.history.length !== 0) {
          result.history.forEach(history => {
            this.history.push(history);
          });
          this.buildHistoryCollection(this.history);
        }
      });
  }

  buildHistoryCollection(history) {
    let dates = history.map(h => this.util.convertMonthYear(h.Date, this.config.Culture));
    dates = dates.filter((item, index) => dates.indexOf(item) === index);

    this.historiesCollection = new Array();

    dates.forEach(date => {
      const histories = history.filter(f => this.util.convertMonthYear(f.Date, this.config.Culture) === date);
      const historyCollection = new HistoryCollection();
      historyCollection.MonthYear = date;
      historyCollection.Histories = histories;

      this.historiesCollection.push(historyCollection);
    });
  }

  getFormattedDate(date: Date): string {
    return this.util.getFormattedDate(date, this.config.GetTranslationByPage(this.pageName, 'lblAt'));
  }

  loadMoreHistory() {
    this.pageIndex++;
    this.loadHistory();
  }

  changePeriod() {
    this.pageIndex = 0;
    this.history = [];
    this.historiesCollection = [];
    this.loadHistory();
  }

  changeFilter(filter) {
    this.filter = filter;
    this.changePeriod();
  }

  openQuickRegister(type) {
    const modal = document.getElementById('modal-' + type + '-quick-register');
    modal.classList.add('opened');
  }
}
