import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ConfigService } from './config.service';
import { BaseService } from './base.service';
import { map, catchError } from 'rxjs/operators';
import { Company } from '../models/company.interface';

@Injectable()
export class CustomerCompanyService extends BaseService {

  constructor(private httpClient: HttpClient, private config: ConfigService) {
    super();
  }

  getCompany(customerId: number) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/companies/${customerId}`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  save(company: Company) {

    const body = JSON.stringify(company);

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/companies`, body
    )
   .pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getRequiredFields(operationId: number) {

    return this.httpClient.get<any>(
        `${this.config.BackendUri}/api/operations/${operationId}/company-required-fields`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }
}
