import { Column } from './column.interface';
import { ColumnType } from './columnType.enum';

export class ColumnCustomer extends Column {

    constructor(
        public columnName: string,
        public customerProviderType: number,
        public initialVisible: boolean,
        public clickable: boolean,
        public IsIcon: boolean,
        public sortable: boolean,
        public columnType: ColumnType
    ) {
        super(columnName, customerProviderType, initialVisible, clickable, IsIcon, sortable, columnType)
    }

    getClassIcon(customer: any): string {
        if (this.IsIcon) {
            if (customer.type === 1) {
                return "icon-user-lines"
            }
            if (customer.type === 2) {
                return "icon-company"
            }
        } else {
            return ""
        }
    }
}