import { FormControlBase } from './form-control-base';

export class CalendarControl extends FormControlBase<string> {
  controlType = 'calendar';
  type: string;
  maskType: string;

  constructor(options: {} = {}) {
    super(options);
    this.type = options['type'] || '';
    this.maskType = options['maskType'] || '';
  }
}
