import { Component, OnInit, ViewChild } from '@angular/core';
import { MatIconRegistry } from '@angular/material/icon';
import { DomSanitizer } from '@angular/platform-browser';
import { ConfigService } from 'src/app/shared/services/config.service';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';
import { BroadcastsService } from 'src/app/shared/services/broadcasts.service';
import { BroadcastDetailsComponent } from './broadcast-details/broadcast-details.component';

@Component({
  selector: 'app-broadcast-news',
  templateUrl: './broadcast-news.component.html',
  styleUrls: ['./broadcast-news.component.css']
})
export class BroadcastNewsComponent implements OnInit {

  pageName = 'Dashboard';
  userId;
  broadcastMessages;
  @ViewChild(BroadcastDetailsComponent) detailsModal: BroadcastDetailsComponent;

  constructor(private config: ConfigService, private util: UtilitiesService,
    private broadcastService: BroadcastsService, private matIconRegistry: MatIconRegistry,
    private domSanitizer: DomSanitizer) {

      this.userId = this.config.JWT.UserId;
      this.matIconRegistry.addSvgIcon(
        'check',
        this.domSanitizer.bypassSecurityTrustResourceUrl('assets/imgs/svg-icons/check.svg')
      );
      this.matIconRegistry.addSvgIcon(
        'double-check',
        this.domSanitizer.bypassSecurityTrustResourceUrl('assets/imgs/svg-icons/double-check.svg')
      );
  }

  ngOnInit(): void {
    this.loadMessages(this.userId);
  }

  getFormattedDate(date: Date): string {
    return this.util.getFormattedDate(date, this.config.GetTranslationByPage('Dashboard', 'lblAt'));
  }

  loadMessages(userId) {
    return this.broadcastService.listReadUnread(userId).subscribe(
      result => {
      if (result) {
        this.broadcastMessages = result;
      }
    });
  }

  markAsRead(broadcastId) {
    this.broadcastService.markAsRead(broadcastId).subscribe(_ => {
        this.loadMessages(this.userId);
    });
  }

  openDetails(broadcastId) {
    this.detailsModal.open(broadcastId);
  }
}
