import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './pages/logins/login/login.component';
import { ForgotPasswordComponent } from './pages/logins/forgot-password/forgot-password.component';
import { NoPermissionComponent } from './pages/logins/no-permission/no-permission.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { CustomersComponent } from './pages/customer/customers/customers.component';
import { RelationsComponent } from './pages/relation/relations/relations.component';
import { CasesComponent } from './pages/cases/cases.component';
import { ArticlesComponent } from './pages/articles/articles.component';
import { CustomerPersonComponent } from './pages/customer/person/customer-person/customer-person.component';
import { CustomerCompanyComponent } from './pages/customer/company/customer-company/customer-company.component';
import { PersonViewComponent } from './pages/customer/person/person-view/person-view.component';
import { CompanyViewComponent } from './pages/customer/company/company-view/company-view.component';
import { ContactViewComponent } from './pages/customer/contact/contact-view/contact-view.component';
import { RelationPhoneCallComponent } from './pages/relation/relation-phone-call/relation-phone-call.component';
import { SelectOperationComponent } from './pages/logins/select-operation/select-operation.component';
import { UserChangePasswordComponent } from './pages/user-change-password/user-change-password.component';
import { AuthGuard } from './auth.guard';

const appRoutes: Routes = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'no-permission', component: NoPermissionComponent },
  { path: 'forgot-password', component: ForgotPasswordComponent },
  { path: 'user-change-password', component: UserChangePasswordComponent},

  { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
  { path: 'customers', component: CustomersComponent, canActivate: [AuthGuard] },
  { path: 'relations', component: RelationsComponent, canActivate: [AuthGuard] },
  { path: 'cases', component: CasesComponent, canActivate: [AuthGuard] },
  { path: 'articles', component: ArticlesComponent, canActivate: [AuthGuard] },
  { path: 'person-view', component: PersonViewComponent, canActivate: [AuthGuard] },
  { path: 'company-view', component: CompanyViewComponent, canActivate: [AuthGuard] },
  { path: 'contact-view', component: ContactViewComponent, canActivate: [AuthGuard] },
  { path: 'customer-person', component: CustomerPersonComponent, canActivate: [AuthGuard] },
  { path: 'customer-company', component: CustomerCompanyComponent, canActivate: [AuthGuard] },
  { path: 'select-operation', component: SelectOperationComponent, canActivate: [AuthGuard] },
  { path: 'relations', component: RelationsComponent, canActivate: [AuthGuard] },
  { path: 'relation-phone-call', component: RelationPhoneCallComponent, canActivate: [AuthGuard] }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);
