import { Router, ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { CustomersService } from 'src/app/shared/services/customers.service';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { Person } from 'src/app/shared/models/person.interface';
import { CustomerPersonService } from 'src/app/shared/services/customer-person.service';

@Component({
  selector: 'app-customer-person',
  templateUrl: './customer-person.component.html',
  styleUrls: ['./customer-person.component.css']
})
export class CustomerPersonComponent implements OnInit {

  pageName = 'CustomerPerson';
  accountablePeople;
  errors;
  savingSuccessful = false;

  // Personal Data
  CustomerId = -1;
  FirstName: string;
  MiddleName: string;
  LastName: string;
  TaxId: string;
  TaxId2: string;
  Gender = -1;
  Email: string;
  Email2: string;
  Phone: string;
  Phone2: string;
  Phone3: string;
  Mobile: string;
  Nickname: string;
  BirthDate: string;
  Website: string;
  Facebook: string;
  Twitter: string;
  Comment: string;
  AccountablePerson = -1;
  Family = -1;

  // Address
  PostalCode: string;
  AddressLine1: string;
  AddressLine2: string;
  AddressNumber: string;
  AddressNumberComp: string;
  City: string;
  State: string;
  Country: string;

  public validation;

  constructor(private router: Router, private config: ConfigService,
    public translate: TranslateService, titleService: Title, private activatedRoute: ActivatedRoute,
    private customersService: CustomersService, private customerPersonService: CustomerPersonService) {

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    titleService.setTitle(config.GetTranslationByPage(this.pageName, 'CustomerPersonTitle'));

    this.loadAccountablePeople();
    this.getPersonRequiredFields();
  }

  ngOnInit() {

    if (this.router.url.includes('customer')) {
      const clientLink = document.querySelector('.clients-link');
      clientLink.classList.add('active');
    }

    this.activatedRoute.queryParams.subscribe((params) => {
      if (params['customerId']) {
        this.CustomerId = +params['customerId'];
        this.loadPerson(this.CustomerId);
      }
    });
  }

  getPersonRequiredFields() {
    this.customerPersonService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.validation = new ValidationService(result.requiredFields);
      }
    });
  }

  searchPostalCode() {
    this.customersService.searchBrazilianPostalCode(this.PostalCode).subscribe(result => {
      this.AddressLine1 = result.address.AddressLine1;
      this.AddressLine2 = result.address.AddressLine2;
      this.City = result.address.City;
      this.State = result.address.State;
      this.Country = result.address.Country;
    });
  }

  loadAccountablePeople() {
    this.customersService.getAccountablePeople(this.config.JWT.OperationID).subscribe(result => {
      this.accountablePeople = result.accountablePeople;
    });
  }

  savePerson({ value, valid }: { value: Person, valid: boolean }) {
    this.errors = '';
    this.savingSuccessful = false;

    if (valid) {

      value.OperationId = this.config.JWT.OperationID;
      value.CustomerId = this.CustomerId;

      if (this.CustomerId === -1) {
        value.CreatedBy = this.config.JWT.UserId;
      } else {
        value.ModifiedBy = this.config.JWT.UserId;
      }

      this.customerPersonService.save(value).subscribe((result: any) => {
        if (result) {
          this.savingSuccessful = true;
        }
      },
        (error) => {
          this.savingSuccessful = false;
          this.errors = this.config.GetTranslationByPage(this.pageName, error);
        });
    }
  }

  loadPerson(customerId: number) {
    this.customerPersonService.getPerson(customerId).subscribe(result => {
      const person = result.person;

      // Personal
      this.FirstName = person.FirstName;
      this.MiddleName = person.MiddleName;
      this.LastName = person.LastName;
      this.TaxId = person.TaxId;
      this.TaxId2 = person.TaxId2;
      this.Gender = person.Gender;
      this.Email = person.Email;
      this.Email2 = person.Email2;
      this.Phone = person.Phone;
      this.Phone2 = person.Phone2;
      this.Phone3 = person.Phone3;
      this.Mobile = person.Mobile;
      this.Nickname = person.Nickname;
      this.BirthDate = person.BirthDate;
      this.Website = person.Website;
      this.Facebook = person.Facebook;
      this.Twitter = person.Twitter;
      this.Comment = person.Comment;
      this.AccountablePerson = person.AccountablePerson;

      // Address
      this.PostalCode = person.PostalCode;
      this.AddressLine1 = person.AddressLine1;
      this.AddressLine2 = person.AddressLine2;
      this.AddressNumber = person.AddressNumber;
      this.AddressNumberComp = person.AddressNumberComp;
      this.City = person.City;
      this.State = person.State;
      this.Country = person.Country;
    });
  }
}
