import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ConfigService } from './config.service';
import { BaseService } from './base.service';
import { map, catchError } from 'rxjs/operators';
import { Contact } from '../models/contact.interface';

@Injectable()
export class CustomerContactService extends BaseService {

  constructor(private httpClient: HttpClient, private config: ConfigService) {
    super();
  }

  getContact(contactId: number) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/contacts/${contactId}`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  save(contact: Contact) {

    const body = JSON.stringify(contact);

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/contacts`, body
    )
   .pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getContacts(customerId: number) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/customers/${customerId}/contacts`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getRequiredFields(operationId: number) {

    return this.httpClient.get<any>(
        `${this.config.BackendUri}/api/operations/${operationId}/contact-required-fields`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }
}
