import { Injectable } from '@angular/core';

@Injectable()
export class UtilitiesService {

    getFullName(firstName, middleName, lastName): string {
        let fullname = firstName;

        if (middleName !== '') {
            fullname += ' ' + middleName;
        }

        if (lastName !== '') {
            fullname += ' ' + lastName;
        }

        return fullname;
    }

    calculateAge(birthDate: Date): number {
        birthDate = new Date(birthDate);
        const timeDiff = Math.abs(Date.now() - birthDate.getTime());
        const age = Math.floor((timeDiff / (1000 * 3600 * 24)) / 365.25);

        return age;
    }

    changeBody(className) {
        const body = document.getElementsByTagName('body')[0];
        body.className = className;
    }

    getIcon(type): string {

        switch (type) {
          case 'CaseClosed':
          case 'CaseLate':
          case 'CaseOpen':
          case 'CaseQueued':
          case 'CaseToday':
            return 'icon-case';
          case 'PhoneIn':
              return 'icon-receive-call';
          case 'PhoneOut':
              return 'icon-phone-call';
          case 'EmailIn':
              return 'icon-receive-email';
          case 'EmailOut':
              return 'icon-phone';
          case 'Chat':
          case 'SmsIn':
          case 'SmsOut':
              return 'icon-chat';
          case 'AppointmentCanceled':
          case 'Appointment':
          case 'SelfAssistance':
              return 'icon-event';
          case 'Basic':
          case 'SocialMedia':
              return 'icon-general';
          case 'Other':
              return 'icon-flag';
        }
    }

    collapseSubject(objDocument, subjectId, collapse) {
        const ulSubject = objDocument.getElementById('ulSubject-' + subjectId);
        const lnkSubject = objDocument.getElementById('lnkSubject-' + subjectId);

        if (ulSubject) {
            if (collapse) {
                ulSubject.classList.remove('opened');
                ulSubject.style.display = 'none';
            } else {
                ulSubject.classList.add('opened');
                ulSubject.style.display = 'block';
            }
        }

        if (lnkSubject) {
            if (collapse) {
                lnkSubject.classList.remove('opened');
            } else {
                lnkSubject.classList.add('opened');
            }
        }
    }

    toggleSubjectView(objDocument, subjectId) {

        const ulSubject = objDocument.getElementById('ulSubject-' + subjectId);
        if (ulSubject) {
            ulSubject.classList.toggle('opened');
            if (ulSubject.style.display === 'block' || ulSubject.style.display === '') {
                ulSubject.style.display = 'none';
            } else {
                ulSubject.style.display = 'block';
            }
        }

        const lnkSubject = objDocument.getElementById('lnkSubject-' + subjectId);
        if (lnkSubject) {
            lnkSubject.classList.toggle('opened');
        }
    }

    birthdayCheck(birthDate): boolean {
        const today = new Date();
        birthDate = new Date(birthDate);
        const todaysDay = today.getDate();
        const todaysMonth = today.getMonth() + 1;

        const birthdayDay = birthDate.getDate();
        const birthdayMonth = birthDate.getMonth() + 1;

        return (birthdayDay === todaysDay && birthdayMonth === todaysMonth);
    }

    // Date Format: dd/MM/yyyy - hh:mm
    formatDate(date): string {
        date = new Date(date);
        const hours = date.getHours();
        const minutes = date.getMinutes();
        const strHours = hours < 10 ? '0' + hours : hours;
        const strMinutes = minutes < 10 ? '0' + minutes : minutes;

        return date.toLocaleDateString() + ' - ' + strHours + ':' + strMinutes;
    }

    // Time format: hh:mm:ss
    formatTime(date): string {
        date = new Date(date);
        const hours = date.getHours();
        const minutes = date.getMinutes();
        const seconds = date.getSeconds();
        const strHours = hours < 10 ? '0' + hours : hours;
        const strMinutes = minutes < 10 ? '0' + minutes : minutes;
        const strSeconds = seconds < 10 ? '0' + seconds : seconds;

        return `${strHours}:${strMinutes}:${strSeconds}`;
    }

    toDatabaseDate(date): string {
        const day = date.split('/')[0];
        const month = date.split('/')[1];
        const year = date.split('/')[2];

        return year + '-' + ('0' + month).slice(-2) + '-' + ('0' + day).slice(-2);
    }

    addDays(date: Date, days: number): Date {
        const newDate = new Date();
        newDate.setDate(date.getDate() + days);
        return newDate;
    }

    convertMonthYear(date: Date, culture): string {
        date = new Date(date);
        return date.toLocaleString(culture, { month: 'long' }) + ' ' + date.getFullYear().toString();
    }

    getFormattedDate(date: Date, lblAt): string {
        date = new Date(date);
        const hours = date.getHours();
        const minutes = date.getMinutes();
        const strHours = hours < 10 ? '0' + hours : hours;
        const strMinutes = minutes < 10 ? '0' + minutes : minutes;

        return date.toLocaleDateString() + ' ' + lblAt + ' ' + strHours + ':' + strMinutes;
    }

    paginate(collection, pageSize: number) {

        const pageCollection = new Array();
        let page;
        for (let i = 0, j = collection.length; i < j; i += pageSize) {
          page = collection.slice(i, i + pageSize);
            pageCollection.push(page);
        }

        return pageCollection;
      }
}
