import { Pipe, PipeTransform } from '@angular/core';
import { TranslatePipe } from '@ngx-translate/core';

@Pipe({
  name: 'OrbiumTranslate'
})

export class OrbiumTranslate extends TranslatePipe implements PipeTransform {

  transform(value: any, args: { page: string }): any {

    const interfaceName = 'AngularDesktop';

    value = interfaceName + '.' + args.page + '.' + value;
    return super.transform(value, args);
  }
}
