/* App Root */
import { routing } from './app.routing';
import { AppComponent } from './app.component';
import { OrbiumTranslateLoader } from './shared/services/orbiumTranslateLoader.service';
import { LoginComponent } from './pages/logins/login/login.component';
import { ForgotPasswordComponent } from './pages/logins/forgot-password/forgot-password.component';
