import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { ConfigService } from './config.service';
import { BaseService } from './base.service';
import { map, catchError } from 'rxjs/operators';
import { timer, Subject } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class SessionsService extends BaseService {

  statusChanger = new Subject();

  constructor(private httpClient: HttpClient, private config: ConfigService) {
    super();
  }

  getCurrentStateByUserId(userId: number) {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/sessions/user/${userId}/current-state`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError)
    );
  }

  getCurrentStateByUserName(username: string) {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/sessions/login/${username}/current-state`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError)
    );
  }

  getChannelLicenses(userId: number) {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/sessions/user/${userId}/channel-licenses`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError)
    );
  }

  sessionChecker(userId: number) {
    const params = new HttpParams().set('userId', userId.toString());

    const source = timer(0, 3000);

    source.subscribe(() => {
      this.httpClient.get<any>(
        `${this.config.BackendUri}/api/sessions/enabled`,
        { params }
      ).subscribe({
        next: res => this.statusChanger.next(res),
        error: err => console.log(err)
      });
    });
  }
}
