import { Injectable, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { fromEvent, merge, timer } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { UserService } from './user.service';
import { ConfigService } from './config.service';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { SessionTimeoutDialogComponent } from '../../../app/components/session-timeout-dialog/session-timeout-dialog.component';

@Injectable({ providedIn: 'root' })
export class IdleService {

  timeout: number;
  lastUserAction = new Date();
  watcherSubscription = null;
  checkerSubscription = null;

  constructor(
    @Inject(DOCUMENT) private document: HTMLDocument,
    private userService: UserService,
    private conifg: ConfigService,
    private router: Router,
    private dialog: MatDialog) { }

  checkIdle() {
    this.userService.getUserTimeout().subscribe(res => {
      if (res.timeout > 0) {
        this.timeout = res.timeout;
        this.watcher();
        this.checker();
      }
    });
  }

  watcher() {
    const mainDiv = this.document.getElementById('mainDiv');
    let subscription = merge(
      ...['click', 'mousemove', 'keydown']
        .map(event => fromEvent(mainDiv, event))
    )
      .pipe(debounceTime(200))
      .subscribe(res => {
        this.lastUserAction = new Date();
      });

    this.watcherSubscription = () => {
      if (subscription) {
        subscription.unsubscribe();
        subscription = null;
      }
    };
  }

  checker() {
    let subscription = timer(0, 1000).subscribe(x => {
      const timeToCompare = this.addMinutes(this.lastUserAction, this.timeout);
      if (timeToCompare < new Date()) {
        this.watcherSubscription();
        this.checkerSubscription();
        this.dialog.open(SessionTimeoutDialogComponent).afterClosed().subscribe(
          (dialogResult) => {
            this.userService.logout(this.conifg.JWT.OperationID).subscribe(res => {
              if (res) {
                window.location.href = '\login';
              }
            });
          });
      }
    });

    this.checkerSubscription = () => {
      if (subscription) {
        subscription.unsubscribe();
        subscription = null;
      }
    };
  }

  addMinutes(dt: Date, minutes: number) {
    return new Date(dt.getTime() + minutes * 60000);
  }

}
