import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CustomerInfo } from 'src/app/shared/models/customer-info.interface';
import { ConfigService } from 'src/app/shared/services/config.service';
import { TranslateService } from '@ngx-translate/core';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { Title } from '@angular/platform-browser';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';
import { RelationsService } from 'src/app/shared/services/relations.service';
import { Contact } from 'src/app/shared/models/contact.interface';
import { CustomersService } from 'src/app/shared/services/customers.service';
import { HistoryCollection } from 'src/app/shared/models/history-collection.interface';
import { RelationPhoneCall } from 'src/app/shared/models/relation-phone-call.interface';
import { CustomerContactService } from 'src/app/shared/services/customer-contact.service';
import { CustomerPersonService } from 'src/app/shared/services/customer-person.service';
import { CustomerCompanyService } from 'src/app/shared/services/customer-company.service';

@Component({
  selector: 'app-relation-phone-call',
  templateUrl: './relation-phone-call.component.html',
  styleUrls: ['./relation-phone-call.component.css']
})

export class RelationPhoneCallComponent implements OnInit {

  pageName = 'RelationPhoneCall';
  personValidation;
  companyValidation;
  relationValidation;

  Relation;
  RelationId;
  CustomerId = -1;
  ContactId = -1;
  Customer: CustomerInfo;
  Contact: Contact;
  CustomerFunctions = [];
  Subjects;
  SubjectItems = [];
  Result = '-1';
  Results;
  CustomerSatisfactionEnabled = false;
  LinkedEdits = [];

  // Histórico
  history = [];
  historiesCollection: HistoryCollection[];
  pageIndex = 0;
  filter = 'all';

  constructor(public config: ConfigService, private router: Router, public translate: TranslateService, titleService: Title,
    public util: UtilitiesService, private relationsService: RelationsService, private customersService: CustomersService,
    private contactService: CustomerContactService,
    private customerPersonService: CustomerPersonService,
    private customerCompanyService: CustomerCompanyService, ) {
    this.config.RelationSubjectItems = [];

    const body = document.getElementsByTagName('body')[0];
    body.className = 'relationships-list-page';

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);
    titleService.setTitle(config.GetTranslationByPage(this.pageName, 'RelationPhoneCallTitle'));

    this.getPersonRequiredFields();
    this.getCompanyRequiredFields();
    this.getRelationRequiredFields();



  }

  ngOnInit() {

    if (this.router.url.includes('relation')) {
      const clientLink = document.querySelector('.relationship-link');
      clientLink.classList.add('active');
    }

    if (this.config.IsLoggedIn) {
      this.createRelationPhoneCall();
    }

  }

  getPersonRequiredFields() {
    this.customerPersonService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.personValidation = new ValidationService(result.requiredFields);
      }
    });
  }

  getCompanyRequiredFields() {
    this.customerCompanyService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.companyValidation = new ValidationService(result.requiredFields);
      }
    });
  }

  getRelationRequiredFields() {
    this.relationsService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.relationValidation = new ValidationService(result.requiredFields);
      }
    });
  }

  createRelationPhoneCall() {

    const rpc = new RelationPhoneCall();
    rpc.OperationId = this.config.JWT.OperationID;
    rpc.PhoneCallId = this.config.PhoneChannel.Id;
    rpc.RelationType = this.config.PhoneChannelType;
    rpc.UserId = this.config.JWT.UserId;
    rpc.Description = this.config.GetTranslationByPage(this.pageName, 'NewPhoneCall');
    rpc.MaxTime = this.config.PhoneChannel.MaxTime;
    rpc.CustomerId = this.CustomerId;
    rpc.ContactId = this.ContactId;
    rpc.ContactId = this.ContactId;
    rpc.CreatedByGroup = this.config.PhoneChannel.GroupId;

    // Dados para teste
    // this.RelationId = 32;
    // this.loadPageData(this.RelationId, this.config.OperationId, this.config.UserId,
    //   this.CustomerId, this.ContactId, this.pageIndex, this.filter);

    this.relationsService.createRelationPhoneCall(rpc).subscribe(result => {
      if (result) {
        this.RelationId = result.relationId;
        this.loadPageData(result.relationId, this.config.JWT.OperationID, this.config.JWT.UserId,
          this.CustomerId, this.ContactId, this.pageIndex, this.filter);
      }
    });
  }

  loadPageData(relationId, operationId, userId, customerId, contactId, pageIndex, filter) {
    this.loadCustomerSatisfaction(relationId, userId);
    this.loadRelation(relationId);
    this.loadCustomerFunctions(relationId);
    this.loadRelationSubjects(relationId);
    this.loadSubjects(userId, relationId);
    this.loadLinkedEdits(operationId, relationId);
    this.loadHistory(customerId, contactId, userId, pageIndex, filter);
  }

  loadCustomer(customerId) {
    this.customersService.getCustomer(customerId).subscribe(result => {
      if (result) {
        this.Customer = result.customer;
        if (this.Relation.ContactId) {
          this.loadContact(this.Relation.ContactId);
        }
      }
    });
  }

  loadContact(contactId) {
    this.contactService.getContact(contactId).subscribe(result => {
      if (result) {
        this.Contact = result.contact;
      }
    });
  }

  loadRelation(relationId) {
    this.relationsService.getRelation(relationId).subscribe(result => {
      if (result) {
        this.Relation = result.relation;
        this.Relation.CallId = this.config.GetTranslationByPage(this.pageName, result.relation.CallId);

        if (this.Relation.CustomerId) {
          this.loadCustomer(this.Relation.CustomerId);
        }

        this.loadResults(this.config.JWT.OperationID, this.config.JWT.UserId);
      }
    });
  }

  loadResults(operationId, userId) {
    this.relationsService.getResults(operationId, userId).subscribe(result => {
      if (result) {
        this.Results = result.results;
        if (this.Relation.Result) {
          this.Result = this.Relation.Result;
        }
      }
    });
  }

  loadCustomerFunctions(relationId) {
    this.relationsService.getCustomerFunctionsByRelation(relationId).subscribe(result => {
      if (result) {
        this.CustomerFunctions = result.customerFunctions;
      }
    });
  }

  loadRelationSubjects(relationId) {
    this.relationsService.getSubjectsByRelation(relationId).subscribe(result => {
      if (result) {
        this.SubjectItems = result.subjects;
        this.config.RelationSubjectItems = this.SubjectItems;
      }
    });
  }

  loadCustomerSatisfaction(relationId, userId) {
    this.relationsService.getCustomerSatisfactionEnabled(relationId, userId).subscribe(result => {
      if (result) {
        this.CustomerSatisfactionEnabled = result;
      }
    });
  }

  // Clientes

  openSelectCustomer() {
    const modal = document.getElementById('modal-select-customer');
    modal.classList.add('opened');
  }

  receiveCustomer($event) {
    const customer = $event;
    this.relationsService.saveCustomer(this.RelationId, customer.Id,
      customer.Contact ? customer.Contact.ContactId : -1).subscribe(result => {
        if (result) {
          this.Customer = customer;
        }
      });
  }

  receiveContact(contact) {
    this.relationsService.saveCustomer(this.RelationId, this.Customer.Id,
      contact.ContactId).subscribe(result => {
        if (result) {
          this.Contact = contact;
        }
      });
  }

  // Assuntos

  openSelectSubjects() {

    this.toggleItemsView(this.Subjects);

    const modal = document.getElementById('modal-select-subjects');
    modal.classList.add('opened');
  }

  loadSubjects(userId, relationId) {
    this.relationsService.getSubjects(userId, relationId).subscribe(result => {
      if (result) {
        const subjects = result.subjects;

        subjects.forEach(subject => {
          subject.Id = 'S' + subject.Id;
          subject.SubjectItems.forEach(subjectItem => {
            subjectItem.ParentId = subject.Id;
          });
        });

        this.Subjects = subjects;
        this.config.RelationAllSubjects = subjects;
        this.toggleItemsView(this.Subjects);
      }
    });
  }

  receiveSubjects() {
    this.SubjectItems = this.config.RelationSubjectItems;
    const subjectIds = this.SubjectItems.map(({ Id }) => Id);
    this.relationsService.saveSubjects(this.config.JWT.OperationID, this.config.JWT.UserId, this.RelationId, subjectIds).subscribe();
  }

  removeSubject(subjectId) {
    this.relationsService.removeRelationSubject(this.RelationId, subjectId).subscribe(result => {
      if (result) {
        this.SubjectItems = this.SubjectItems.filter(i => i.Id !== subjectId);
        this.config.RelationSubjectItems = this.SubjectItems;
      }
    });
  }

  toggleItemsView(subjects) {

    if (subjects && subjects.length > 0) {
      subjects.forEach(subject => {
        this.util.collapseSubject(document, subject.Id, subject.ParentId !== null);
        if (subject.SubjectItems) {
          this.toggleItemsView(subject.SubjectItems);
        }
      });
    }
  }

  // Papéis

  openSelectCustomerFunction() {
    const modal = document.getElementById('modal-select-customer-function');
    modal.classList.add('opened');
  }

  receiveCustomerFunction($event) {

    const customerFunction = $event;

    if (this.validateCustomerFunction(customerFunction)) {

      this.relationsService.saveCustomerFunction(this.RelationId, customerFunction.CustomerId, this.Contact ? this.Contact.ContactId : -1,
        this.config.JWT.UserId, customerFunction.CustomerType, customerFunction.FunctionName)
        .subscribe(result => {
          if (result) {
            customerFunction.Id = result;
            this.CustomerFunctions.push(customerFunction);
          }
        });
    }
  }

  validateCustomerFunction(customerFunction): boolean {
    return this.CustomerFunctions.filter(f => f.FunctionId === customerFunction.FunctionId
      && f.CustomerId === customerFunction.CustomerId).length === 0;
  }

  removeFunction(customerFunctionId) {
    this.relationsService.removeCustomerFunction(customerFunctionId).subscribe(result => {
      if (result) {
        this.CustomerFunctions = this.CustomerFunctions.filter(i => i.Id !== customerFunctionId);
      }
    });
  }

  // Resultado

  saveResult() {
    if (this.Result !== '-1') {
      this.relationsService.saveResult(this.RelationId, this.Result).subscribe();
    }
  }

  // Observações

  openEditNotes() {
    const modal = document.getElementById('modal-edit-notes');
    modal.classList.add('opened');
  }

  receiveNotes(notes) {
    this.Relation.Notes = notes;
    this.relationsService.saveNotes(this.RelationId, notes).subscribe();
  }

  // Histórico

  loadHistory(customerId, contactId, userId, pageIndex, filter) {

    this.customersService.getHistory(customerId, contactId, userId, 0,
      pageIndex, filter).subscribe(result => {

        if (result.history.length !== 0) {
          result.history.forEach(history => {
            this.history.push(history);
          });
          this.buildHistoryCollection(this.history);
        }
      });
  }

  buildHistoryCollection(history) {
    let dates = history.map(h => this.util.convertMonthYear(h.Date, this.config.Culture));
    dates = dates.filter((item, index) => dates.indexOf(item) === index);

    this.historiesCollection = new Array();

    dates.forEach(date => {
      const histories = history.filter(f => this.util.convertMonthYear(f.Date, this.config.Culture) === date);
      const historyCollection = new HistoryCollection();
      historyCollection.MonthYear = date;
      historyCollection.Histories = histories;

      this.historiesCollection.push(historyCollection);
    });
  }

  getFormattedDate(date: Date): string {
    return this.util.getFormattedDate(date, this.config.GetTranslationByPage(this.pageName, 'lblAt'));
  }

  loadMoreHistory() {
    this.pageIndex++;
    this.loadHistory(this.CustomerId, this.ContactId, this.config.JWT.UserId, this.pageIndex, this.filter);
  }

  changeFilter(filter) {
    this.filter = filter;
    this.history = [];
    this.historiesCollection = [];
    this.loadHistory(this.CustomerId, this.ContactId, this.config.JWT.UserId, this.pageIndex, this.filter);
  }

  // Linked Data

  loadLinkedEdits(operationId, relationId) {
    this.relationsService.getLinkedEdits(operationId, relationId).subscribe(result => {
      if (result) {
        result.linkedEdits.forEach(linkedEdit => {
          if (linkedEdit.Enabled) {
            this.LinkedEdits.push(linkedEdit);
          }
        });
      }
    });
  }

  openLinkedEdit(index) {
    const modal = document.getElementById('modal-linked-edit-' + index);
    modal.classList.add('opened');
  }

  receiveLinkedEdits(linkedEdit) {
    let modifiedLinkedEdit = this.LinkedEdits.find(f => f.Index === linkedEdit.Index);
    modifiedLinkedEdit = linkedEdit;
    this.relationsService.saveLinkedEdit(this.RelationId, modifiedLinkedEdit.Index, modifiedLinkedEdit.Value).subscribe();
  }

  linkedEditFocusOut(name, index, value) {

    if (this.relationValidation.requiredValidation(name, value)) {
      this.relationsService.saveLinkedEdit(this.RelationId, index, value).subscribe();
    }
  }
}
