import { Component, Output, EventEmitter, Input, OnInit } from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { RelationsService } from 'src/app/shared/services/relations.service';
import { ValidationService } from 'src/app/shared/services/validation.service';

@Component({
  selector: 'app-linked-edit',
  templateUrl: './linked-edit.component.html',
  styleUrls: ['./linked-edit.component.css']
})
export class LinkedEditComponent implements OnInit {

  pageName = 'LinkedEdit';
  validation;

  @Input()
  LinkedEdit;

  LinkedValue;
  LinkedOptions = [];

  @Output()
  messageEvent = new EventEmitter<string>();

  constructor(private config: ConfigService, private relationService: RelationsService) { }

  ngOnInit() {
    if (this.LinkedEdit) {
      this.getRelationRequiredFields();
      if (this.LinkedEdit.Style === 1) {
        this.loadLinkedEditContent(this.config.JWT.OperationID, this.LinkedEdit.RelationId, this.LinkedEdit.Index);
      } else {
        this.LinkedValue = this.LinkedEdit.Value;
      }
    }
  }

  getRelationRequiredFields() {
    this.relationService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.validation = new ValidationService(result.requiredFields);
      }
    });
  }

  closeModal() {
    const modal = document.getElementById('modal-linked-edit-' + this.LinkedEdit.Index);
    modal.classList.remove('opened');
  }

  loadLinkedEditContent(operationId, relationId, index) {
    this.relationService.getLinkedEditContent(operationId, relationId, index).subscribe(result => {

      if (result) {
        this.LinkedOptions = result.dataSource;

        if (this.LinkedEdit.Value === '' || !this.LinkedOptions.find(f => f.Value === this.LinkedEdit.Value)) {
          this.LinkedValue = -1;
        }
      }
    });
  }

  saveLinkedEdit(valid: boolean) {

    if (valid && this.validation.requiredValidation('LinkedEdit' + this.LinkedEdit.Index, this.LinkedValue)) {
      this.LinkedEdit.Value = this.LinkedValue;
      this.messageEvent.emit(this.LinkedEdit);
      this.closeModal();
    }
  }

}
