import { Component, Input, OnDestroy } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from '../../shared/services/config.service';

@Component({
  selector: 'app-spinner',
  templateUrl: './spinner.component.html',
  styleUrls: ['./spinner.component.scss']
})
export class SpinnerComponent implements OnDestroy {
  componentName = 'Spinner';
  private currentTimeout: number;
  public isDelayedRunning = false;

  constructor(config: ConfigService, public translate: TranslateService) {
    translate.getTranslation(config.Culture);
    translate.use(config.Culture);
  }

  @Input() showLoadingMessage = true;
  @Input() waitingMessageColor = '';

  @Input()
  public padding;

  @Input()
  public containerStyle: string;

  @Input()
  public delay: 150;

  @Input()
  public set isRunning(value: boolean) {
    if (!value) {
      this.cancelTimeout();
      this.isDelayedRunning = false;
      return;
    }

    if (this.currentTimeout) {
      return;
    }

    // specify window to side-step conflict with node types: https://github.com/mgechev/angular2-seed/issues/901
    this.currentTimeout = window.setTimeout(() => {
      const container = document.querySelector('.' + this.containerStyle);
      container.classList.add('loading');
      this.isDelayedRunning = value;
      this.cancelTimeout();
    }, this.delay);
  }

  private cancelTimeout(): void {
    clearTimeout(this.currentTimeout);
    this.currentTimeout = undefined;
  }

  ngOnDestroy(): any {
    this.cancelTimeout();
  }
}
