import { Component, EventEmitter, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { RelationsService } from 'src/app/shared/services/relations.service';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { CustomerInfo } from 'src/app/shared/models/customer-info.interface';
import { CustomerFunction } from 'src/app/shared/models/customer-function.interface';
import { CustomerType } from 'src/app/shared/models/customer-type.enum';

@Component({
  selector: 'app-select-customer-function',
  templateUrl: './select-customer-function.component.html',
  styleUrls: ['./select-customer-function.component.css']
})
export class SelectCustomerFunctionComponent {

  pageName = 'SelectCustomer';
  customerName: string;
  pageMode = 'customer';
  Customer: CustomerInfo;
  FunctionId = -1;
  Functions;
  validation;

  @Output() messageEvent = new EventEmitter<CustomerFunction>();

  constructor(public config: ConfigService, public translate: TranslateService, private relationsService: RelationsService) {
    this.loadCustomerFunctions(config.JWT.OperationID);
    this.validation = new ValidationService();
    this.validation.addRequiredField('FunctionId');
  }

  closeModal() {
    const modal = document.getElementById('modal-select-customer-function');
    modal.classList.remove('opened');
    this.pageMode = 'customer';
    this.FunctionId = -1;
  }

  receiveMessage($event) {
    this.Customer = $event;
    this.pageMode = 'function';
  }

  loadCustomerFunctions(operationId) {
    this.relationsService.getCustomerFunctions(operationId).subscribe( result => {
      if (result) {
        this.Functions = result.customerFunctions;
      }
    });
  }

  addCustomerFunction({ value, valid }: { value: any, valid: boolean }) {

    if (valid && this.validation.validateSave(CustomerType.Person, value)) {

      const customerFunction = new CustomerFunction();

      customerFunction.CustomerId = this.Customer.Id;
      customerFunction.CustomerName = this.Customer.Name;
      customerFunction.CustomerType = this.Customer.Type;

      const functionInfo = this.Functions.filter(i => i.Id === +value.FunctionId)[0];

      customerFunction.FunctionId = functionInfo.Id;
      customerFunction.FunctionName = functionInfo.Name;

      this.messageEvent.emit(customerFunction);
      this.closeModal();
    }
  }

  backToCustomer() {
    this.pageMode = 'customer';
  }

}
