import { Component, EventEmitter, Output } from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-select-person',
  templateUrl: './select-person.component.html',
  styleUrls: ['./select-person.component.css']
})
export class SelectPersonComponent {

  pageName = 'SelectCustomer';
  @Output() messageEvent = new EventEmitter<string>();

  constructor(public config: ConfigService, public translate: TranslateService) { }

  closeModal() {
    const modal = document.getElementById('modal-select-client');
    modal.classList.remove('opened');
  }

  receiveMessage($event) {
    this.messageEvent.emit($event);
    this.closeModal();
  }
}
