import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { ConfigService } from './config.service';
import { BaseService } from './base.service';
import { map, catchError } from 'rxjs/operators';
import { RelationPhoneCall } from '../models/relation-phone-call.interface';

@Injectable()
export class RelationsService extends BaseService {

  constructor(private httpClient: HttpClient, private config: ConfigService) {
    super();
  }

  getRelation(relationId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relations/${relationId}`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  createRelationPhoneCall(relationPhonecall: RelationPhoneCall) {

    const body = JSON.stringify(relationPhonecall);

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/relations/create-relation-phone-call`, body
      )
   .pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getCustomerSatisfactionEnabled(relationId, userId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/user/${userId}/customer-satisfaction-enabled`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getResults(operationId, userId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relations/operation/${operationId}/user/${userId}/results`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getSubjects(userId, relationId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/user/${userId}/subjects`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getSubjectsByRelation(relationId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/subjects`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getCustomerFunctions(operationId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relations/operation/${operationId}/customer-functions`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getCustomerFunctionsByRelation(relationId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/customer-functions`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  saveCustomer(relationId, customerId, contactId) {

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/customer/${customerId}/contact/${contactId}/save-customer`, null
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  saveCustomerFunction(relationId, customerId, contactId, userId, customerType, customerFunction) {

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/customer/${customerId}/contact/${contactId}
      /user/${userId}/customer-type/${customerType}/customer-function/${customerFunction}/save`, null
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  removeCustomerFunction(customerFunctionId) {

    return this.httpClient.delete(
      `${this.config.BackendUri}/api/relations/customer-function/${customerFunctionId}/delete`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  saveSubjects(OperationId, UserId, RelationId, SubjectIds) {

    const body = JSON.stringify({ OperationId, UserId, RelationId, SubjectIds});

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/relations/subjects/save`, body
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  removeRelationSubject(relationId, subjectItemId) {

    return this.httpClient.delete(
      `${this.config.BackendUri}/api/relations/${relationId}/subject-item/${subjectItemId}/delete`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  saveResult(relationId, strResult) {

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/result/${strResult}/save`, null
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  saveNotes(relationId, notes) {

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/notes/${notes}/save`, null
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getLinkedEdits(operationId, relationId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/operation/${operationId}/linked-edits`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getLinkedEditContent(operationId, relationId, index) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/operation/${operationId}/linked-edit/${index}`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  saveLinkedEdit(relationId, index, value) {

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/relations/${relationId}/index/${index}/value/${value}/save`, null
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getRequiredFields(operationId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/operations/${operationId}/relation-required-fields`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getSummaryByDate(userId: number, startedOn: string, endedOn: string) {
    const params = new HttpParams().set('userId', userId.toString()).set('startedOn', startedOn).set('endedOn', endedOn);

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/relationssummary/summary-by-date`, { params }
    ).pipe(catchError(this.handleError));
  }

  getSummaryByTypeAndDate(userId: number, relType: number, startDate: string, endDate: string) {
    const params = new HttpParams().set('userId', userId.toString())
                                  .set('relType', relType.toString())
                                  .set('startDate', startDate)
                                  .set('endDate', endDate);

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/Relationssummary/summary-by-type-and-date`, { params }
    ).pipe(catchError(this.handleError));
  }
}
