import { IColumn } from './IColumn'
import { ColumnType } from './columnType.enum'

export class Column implements IColumn {

    constructor(
        public columnName: string,
        public customerProviderType: number,
        public initialVisible: boolean,
        public isClickable: boolean,
        public isIcon: boolean,
        public isSortable: boolean,
        public columnType: ColumnType
    ) {
        this.columnName = columnName
        this.customerProviderType = customerProviderType
        this.isClickable = isClickable
        this.initialVisible = initialVisible
        this.isIcon = isIcon
        this.isSortable = isSortable
        this.columnType = columnType
    }

    getClassIcon(row: any): string {
        return '';
    }

    onClick(): void { }
}