import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-timer-widget',
  templateUrl: './timer-widget.component.html',
  styleUrls: ['./timer-widget.component.css']
})

export class TimerWidgetComponent implements OnChanges {

  @Input() value: number;
  radius = 54;
  circunference = 2 * Math.PI * this.radius;
  dashoffset: number;
  progressText;
  progressColor;
  progressFilter;
  timeoutAnimation = null;

  constructor() {
    this.timerProgress(0);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.value.currentValue !== changes.value.previousValue) {
      this.timerProgress(changes.value.currentValue);
    }
  }

  private timerProgress(value: number) {
    let newColor;
    const progress = value / 100;

    if ( value <= 100 ) {
      this.dashoffset = this.circunference * (1 - progress);
    } else if (this.timeoutAnimation === null) {
      this.timeoutAnimation = 'timeout 0.6s ease-in-out infinite alternate';
    }

    const minutes = parseInt((value / 60).toString(), 10);
    const seconds = parseInt((value % 60).toString(), 10);
    const strMinutes = minutes < 10 ? '0' + minutes : minutes;
    const strSeconds = seconds < 10 ? '0' + seconds : seconds;

    if (value > 60) {
      newColor = 'red';
    } else if (value > 30) {
      newColor = 'yellow';
    } else {
      newColor = 'green';
    }

    if (newColor !== this.progressColor) {
      this.changeColor(newColor);
    }

    this.progressText = strMinutes + ':' + strSeconds;
  }

  changeColor(color: string) {

    switch (color) {
      case 'green': {
        this.progressFilter = 'url(#green-glow)';
        this.progressColor = '#00ff06';
        break;
      }
      case 'yellow': {
        this.progressFilter = 'url(#yellow-glow)';
        this.progressColor = '#fef100';
        break;
      }
      case 'red': {
        this.progressFilter = 'url(#red-glow)';
        this.progressColor = '#ff0a0a';
        break;
      }
      default: {
        break;
      }
    }
  }
}
