import { Component, Input } from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';
import { RelationsService } from 'src/app/shared/services/relations.service';
import { EChartOption } from 'echarts';

@Component({
  selector: 'app-relations-performance',
  templateUrl: './relations-performance.component.html',
  styleUrls: ['./relations-performance.component.css']
})
export class RelationsPerformanceComponent {

  pageName = 'Dashboard';
  startDate;
  endDate;
  avgDuration;
  avgDurationGoal;
  sumRelation;
  sumRelationGoal;
  relationsInQueue;
  isAvgDurationOnGoal;
  isSumRelationOnGoal;
  chartData: any[] = [];
  chartOptions: EChartOption;
  noData: Boolean = true;

  _timeInterval = 'today';
  @Input()
  set timeInterval(value) {
    this._timeInterval = value;
    this.setDatePeriod();
    this.getSummaryByTypeAndDate(this.relationType, this.startDate, this.endDate);
  }
  get timeInterval() {
    return this._timeInterval;
  }

  _relationType = 0;
  @Input() set relationType(value) {
    this._relationType = value;
    this.getSummaryByTypeAndDate(this._relationType, this.startDate, this.endDate);
  }

  get relationType() {
    return this._relationType;
  }

  constructor(private config: ConfigService,
    private utilServices: UtilitiesService,
    private relationsService: RelationsService) {
      this.setDatePeriod();
      this.getSummaryByTypeAndDate(this.relationType, this.startDate, this.endDate);
      this.chartData.push(['data', '']);
  }

  setDatePeriod() {

    const today = new Date();

    switch (this.timeInterval) {
      case 'today': {
        this.startDate = this.utilServices.toDatabaseDate(today.toLocaleDateString());
        this.endDate = this.utilServices.toDatabaseDate(today.toLocaleDateString());
        break;
      }
      case 'yesterday': {
        const yesterday = this.utilServices.addDays(today, -1);
        this.startDate = this.utilServices.toDatabaseDate(yesterday.toLocaleDateString());
        this.endDate = this.utilServices.toDatabaseDate(yesterday.toLocaleDateString());
        break;
      }
      case 'week': {
        const week = this.utilServices.addDays(today, -7);
        this.startDate = this.utilServices.toDatabaseDate(week.toLocaleDateString());
        this.endDate = this.utilServices.toDatabaseDate(today.toLocaleDateString());
        break;
      }
      case 'month': {
        const month = this.utilServices.addDays(today, -30);
        this.startDate = this.utilServices.toDatabaseDate(month.toLocaleDateString());
        this.endDate = this.utilServices.toDatabaseDate(today.toLocaleDateString());
        break;
      }
    }
  }

  getSummaryByTypeAndDate(relType, startDate, endDate) {

    this.relationsService.getSummaryByTypeAndDate(this.config.JWT.UserId, relType, startDate, endDate)
      .subscribe(result => {
        if (result) {
          this.avgDuration = result.formattedAvgDuration;
          this.sumRelation = result.sumRelation;
          this.relationsInQueue = result.relationsInQueue;
          this.avgDurationGoal = result.formattedAvgDurationGoal;
          this.isAvgDurationOnGoal = result.avgDuration <= result.avgDurationGoal;
          this.sumRelationGoal = result.sumRelationGoal;
          this.isSumRelationOnGoal = result.sumRelations >= result.sumRelationGoal;
          this.noData = result.summaryPerDay.length === 0;

          if (!this.noData) {
            this.loadServicesChart(result.dailySumRelationGoal, result.summaryPerDay);
          }
        }
    });
  }

  loadServicesChart(servicesGoal, servicesPerDay) {

    const days = [];
    const serviceGoals = [];
    const services = [];

    const servicesLabel = this.config.GetTranslationByPage('Dashboard', 'lblServices');
    const targetLabel = this.config.GetTranslationByPage('Dashboard', 'lblTarget');

    servicesPerDay.forEach(service => {
      days.push(service.key);
      services.push(service.value);

      if (this.relationType !== 0) {
        serviceGoals.push(servicesGoal);
      }
    });

    this.chartOptions = {
      legend: {
        show: true,
        bottom: 2,
        symbolKeepAspect: false,
        data: []
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: days
      },
      yAxis: {
        type: 'value'
      },
      tooltip: {
        show: true
      },
      series: [
        {
          name: targetLabel,
          data: serviceGoals,
          type: 'line',
          color: '#006cff',
          lineStyle: {
            width: 3,
            color: '#006cff'
          },
          areaStyle: {
            color: {
              type: 'linear',
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [{
                offset: 0, color: 'rgba(0, 108, 255, 0.2)' // color at 0% position
              }, {
                offset: 1, color: 'rgba(0, 108, 255, 0)' // color at 100% position
              }
            ]}
          },
          smooth: true,
          showSymbol: false
        },
        {
          name: servicesLabel,
          data: services,
          type: 'line',
          color: '#ff8843',
          lineStyle: {
            width: 3,
            color: '#ff8843'
          },
          areaStyle: {
            color: {
              type: 'linear',
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [{
                offset: 0, color: 'rgba(255, 136, 67, 0.2)' // color at 0% position
              }, {
                offset: 1, color: 'rgba(255, 136, 67, 0)' // color at 100% position
              }]
            }
          },
          smooth: true,
          symbol: 'circle',
          symbolSize: 10,
          label: {
            normal: {
                show: true,
                position: 'top'
            }
          }
        }
      ]
    };

    if (this.relationType === 0) {
      this.chartOptions.legend.data = [{
          name: servicesLabel,
          icon: 'roundRect'
        }];
    } else {
      this.chartOptions.legend.data = [{
        name: servicesLabel,
        icon: 'roundRect'
      }, {
        name: targetLabel,
        icon: 'roundRect'
      }];
    }
  }
}
