import { Component, OnInit } from '@angular/core';
import { CasesService } from 'src/app/shared/services/cases.service';
import { ConfigService } from 'src/app/shared/services/config.service';
import { CustomersService } from 'src/app/shared/services/customers.service';

@Component({
  selector: 'app-performance-cases',
  templateUrl: './performance-cases.component.html',
  styleUrls: ['./performance-cases.component.css']
})
export class PerformanceCasesComponent implements OnInit {

  pageName = 'Today';
  openedCaseList: any[] = [];
  closedCaseList: any[] = [];
  dueCaseList: any[] = [];
  caseType = 'opened';

  constructor(private caseServices: CasesService, private configServices: ConfigService, private customersService: CustomersService) { }

  ngOnInit(): void {
    this.getOpenCases();
    this.getDueCases();
    this.getClosedCases();
  }

  changeCaseType(caseType) {
    this.caseType = caseType;
  }

  getList() {
    switch (this.caseType) {
      case 'opened':
        return this.openedCaseList;
      case 'due':
        return this.dueCaseList;
      case 'closed':
        return this.closedCaseList;
      default:
        break;
    }
  }

  getOpenCases() {
    this.caseServices.getOpenByUser(this.configServices.JWT.UserId, 'CreatedOn', 'asc', 'pt-br', 0, 5)
      .subscribe(result => {
        result.map((item: any) => {
          this.customersService.getCustomer(item.relationCustomerId).subscribe(resp => {
            item['customerName'] = resp.customer.Name;
            this.openedCaseList.push(item);
          });
        });
      });
  }

  getDueCases() {
    this.caseServices.getDueByUser(this.configServices.JWT.UserId, 'CreatedOn', 'asc', 'pt-br', 0, 5)
      .subscribe(result => {
        result.map((item: any) => {
          this.customersService.getCustomer(item.relationCustomerId).subscribe(resp => {
            item['customerName'] = resp.customer.Name;
            this.dueCaseList.push(item);
          });
        });
      });
  }

  getClosedCases() {
    const currentDate = new Date();
    const tomorrowDate = new Date();
    tomorrowDate.setDate(tomorrowDate.getDate() + 1);

    this.caseServices.getClosedByUser(
      this.configServices.JWT.UserId,
      this.formatDateYearFirst(currentDate),
      this.formatDateYearFirst(tomorrowDate),
      'CreatedOn',
      'desc',
      'pt-br',
      0,
      5
    )
      .subscribe(result => {
        result.map((item: any) => {
          this.customersService.getCustomer(item.relationCustomerId).subscribe(resp => {
            item['customerName'] = resp.customer.Name;
            this.closedCaseList.push(item);
          });
        });
      });
  }

  // dd/mm/yyyy hh:mm:ss
  formatDateDayFirst(inputDate) {
    const date = new Date(inputDate);
    if (!isNaN(date.getTime())) {
      const day = (date.getDate() < 10 ? '0' : '') + date.getDate();
      const month = (date.getMonth() + 1 < 10 ? '0' : '') + date.getMonth();
      const year = date.getFullYear();
      const hour = date.getHours();
      const min = (date.getMinutes() < 10 ? '0' : '') + date.getMinutes();
      const sec = (date.getSeconds() < 10 ? '0' : '') + date.getSeconds();
      return `${day}/${month}/${year} às ${hour}:${min}:${sec}`;
    }
  }

  // yyyy-mm-dd
  formatDateYearFirst(inputDate) {
    const d = new Date(inputDate);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    const year = d.getFullYear();

    if (month.length < 2) {
      month = '0' + month;
    }
    if (day.length < 2) {
      day = '0' + day;
    }

    return [year, month, day].join('-');
  }
}
