import { Component, Output, EventEmitter, Input } from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';

@Component({
  selector: 'app-select-subjects',
  templateUrl: './select-subjects.component.html',
  styleUrls: ['./select-subjects.component.css']
})
export class SelectSubjectsComponent {

  pageName = 'SelectSubjects';
  @Input() Subjects;
  @Output() messageEvent = new EventEmitter<Array<any>>();

  filteredSubjects = [];

  constructor(private config: ConfigService, private util: UtilitiesService) { }

  filterSubjects(filter) {

    if (filter === '') {
      this.Subjects = this.config.RelationAllSubjects;
    } else {
      this.filteredSubjects = [];
      this.filterSubjectItems(this.config.RelationAllSubjects, filter);
      this.Subjects = [...this.filteredSubjects];
    }
  }

  filterSubjectItems(subjects, filter) {

    subjects.forEach(subject => {
      if (subject.Name.toUpperCase().startsWith(filter.toUpperCase())) {

        if (subject.ParentId && !subject.HasChildren) {
          this.addParent(subject.ParentId, subject);
        } else {
          this.addFilteredSubject(subject);
        }
      } else if (subject.HasChildren) {
        this.filterSubjectItems(subject.SubjectItems, filter);
      }
    });
  }

  addFilteredSubject(subject) {
    if (!this.getSubjectById(subject.Id, this.filteredSubjects)) {
      this.filteredSubjects.push(subject);
    }
  }

  addParent(parentId, subject) {

    if (parentId && !this.getSubjectById(parentId, this.filteredSubjects)) {

      const parentSubject = this.getSubjectById(parentId, this.config.RelationAllSubjects);

      if (parentSubject) {
        parentSubject.SubjectItems = [];
        parentSubject.SubjectItems.push(subject);
        this.addFilteredSubject(parentSubject);
        this.addParent(parentSubject.Id, parentSubject);
      }
    }
  }

  getSubjectById(subjectId, subjects) {

    if (subjects) {
      let subject = subjects.find(i => i.Id === subjectId);

      if (subject) {
        return subject;
      } else {
        subjects.forEach(subjectItem => {
          subject = this.getSubjectById(subjectId, subjectItem.SubjectItems);
          if (subject) {
            return subject;
          }
        });
      }

      return subject;
    }
  }

  closeModal() {
    const modal = document.getElementById('modal-select-subjects');
    modal.classList.remove('opened');
  }

  selectSubjects() {
    this.messageEvent.emit();
    this.closeModal();
  }

  cancel() {
    this.closeModal();
  }

}
