import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { ConfigService } from './config.service';
import { BaseService } from './base.service';
import { map, catchError } from 'rxjs/operators';
import { CustomerSearch } from '../models/customer-search.interface';

@Injectable()
export class CustomersService extends BaseService {

  constructor(private httpClient: HttpClient, private config: ConfigService) {
    super();
  }

  getCustomer(customerId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/customers/${customerId}`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getSearchTypes() {
    const params = new HttpParams().set('operationId', this.config.JWT.OperationID);
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/customers/search-types`, { params }
    ).pipe(catchError(this.handleError));
  }

  getSearchColumns() {
    const params = new HttpParams().set('operationId', this.config.JWT.OperationID).set('userId', this.config.JWT.UserId);
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/customers/result-columns`, { params }
    ).pipe(catchError(this.handleError));
  }

  search(searchParams: CustomerSearch) {

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/customers/search`, JSON.stringify(searchParams)
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  searchByName(searchParams: CustomerSearch) {

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/customers/search-by-name`, JSON.stringify(searchParams)
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  searchBrazilianPostalCode(postalCode: string) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/customers/postal-code/${postalCode}/address`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getAccountablePeople(operationId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/customers/operation/${operationId}/accountable-people`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getHistory(customerId: number, contactId: number, userId: number, days: number, pageIndex: number, filter: string) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/customers/${customerId}/${contactId}/${userId}/${days}/${pageIndex}/${filter}/history`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }
}
