import { Router, ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { CustomersService } from 'src/app/shared/services/customers.service';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { Company } from 'src/app/shared/models/company.interface';
import { CustomerCompanyService } from 'src/app/shared/services/customer-company.service';

@Component({
  selector: 'app-customer-company',
  templateUrl: './customer-company.component.html',
  styleUrls: ['./customer-company.component.css']
})
export class CustomerCompanyComponent implements OnInit {

  pageName = 'CustomerCompany';
  accountablePeople;
  errors;
  savingSuccessful = false;

  // Company Data
  CustomerId = -1;
  Name: string;
  TradeName: string;
  TaxId: string;
  TaxId2: string;
  TaxId3: string;
  Email: string;
  Email2: string;
  Phone: string;
  Phone2: string;
  Phone3: string;
  AnnualRevenue: string;
  Employees: string;
  Industry: string;
  Website: string;
  Facebook: string;
  Twitter: string;
  Comment: string;
  AccountablePerson = -1;
  Family = -1;

  // Address
  PostalCode: string;
  AddressLine1: string;
  AddressLine2: string;
  AddressNumber: string;
  AddressNumberComp: string;
  City: string;
  State: string;
  Country: string;

  public validation;

  constructor(private router: Router, private config: ConfigService, public translate: TranslateService,
    titleService: Title, private activatedRoute: ActivatedRoute, private customersService: CustomersService,
    private customerCompanyService: CustomerCompanyService) {

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    titleService.setTitle(config.GetTranslationByPage(this.pageName, 'CustomerCompanyTitle'));

    this.loadAccountablePeople();
    this.getCompanyRequiredFields();
  }

  ngOnInit() {

    if (this.router.url.includes('customer')) {
      const clientLink = document.querySelector('.clients-link');
      clientLink.classList.add('active');
    }

    this.activatedRoute.queryParams.subscribe((params) => {
      if (params['customerId']) {
        this.CustomerId = +params['customerId'];
        this.loadCompany(this.CustomerId);
      }
    });
  }

  getCompanyRequiredFields() {
    this.customerCompanyService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.validation = new ValidationService(result.requiredFields);
      }
    });
  }

  searchPostalCode() {
    this.customersService.searchBrazilianPostalCode(this.PostalCode).subscribe(result => {
      this.AddressLine1 = result.address.AddressLine1;
      this.AddressLine2 = result.address.AddressLine2;
      this.City = result.address.City;
      this.State = result.address.State;
      this.Country = result.address.Country;
    });
  }

  loadAccountablePeople() {
    this.customersService.getAccountablePeople(this.config.JWT.OperationID).subscribe(result => {
      this.accountablePeople = result.accountablePeople;
    });
  }

  saveCompany({ value, valid }: { value: Company, valid: boolean }) {
    this.errors = '';
    this.savingSuccessful = false;

    if (valid) {

      value.OperationId = this.config.JWT.OperationID;
      value.CustomerId = this.CustomerId;

      if (this.CustomerId === -1) {
        value.CreatedBy = this.config.JWT.UserId;
      } else {
        value.ModifiedBy = this.config.JWT.UserId;
      }

      this.customerCompanyService.save(value).subscribe((result: any) => {
        if (result) {
          this.savingSuccessful = true;
        }
      },
        (error) => {
          this.savingSuccessful = false;
          this.errors = this.config.GetTranslationByPage(this.pageName, error);
        });
    }
  }

  loadCompany(customerId: number) {
    this.customerCompanyService.getCompany(customerId).subscribe(result => {
      const company = result.company;

      // Company
      this.Name = company.Name;
      this.TradeName = company.TradeName;
      this.TaxId = company.TaxId;
      this.TaxId2 = company.TaxId2;
      this.TaxId3 = company.TaxId3;
      this.Email = company.Email;
      this.Email2 = company.Email2;
      this.Phone = company.Phone;
      this.Phone2 = company.Phone2;
      this.Phone3 = company.Phone3;
      this.Industry = company.Industry;
      this.AnnualRevenue = company.AnnualRevenue;
      this.Employees = company.Employees;
      this.Website = company.Website;
      this.Facebook = company.Facebook;
      this.Twitter = company.Twitter;
      this.Comment = company.Comment;
      this.AccountablePerson = company.AccountablePerson;

      // Address
      this.PostalCode = company.PostalCode;
      this.AddressLine1 = company.AddressLine1;
      this.AddressLine2 = company.AddressLine2;
      this.AddressNumber = company.AddressNumber;
      this.AddressNumberComp = company.AddressNumberComp;
      this.City = company.City;
      this.State = company.State;
      this.Country = company.Country;
    });
  }
}
