import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { ConfigService } from './config.service';
import { catchError } from 'rxjs/operators';
import { BaseService } from './base.service';

@Injectable({ providedIn: 'root' })
export class BroadcastsService extends BaseService {

  constructor(private httpClient: HttpClient, private config: ConfigService) {
    super();
  }

  listReadUnread(userId) {

    const params = new HttpParams().set('userId', userId);

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/broadcasts/list-read-unread`, { params }
    ).pipe(catchError(this.handleError));
  }

  markAsRead(broadcastId) {

    const params = new HttpParams().set('broadcastId', broadcastId);

    return this.httpClient.put<any>(
      `${this.config.BackendUri}/api/broadcasts/mark-as-read`, null , { params }
    ).pipe(catchError(this.handleError));
  }

  get(broadcastId) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/broadcasts/${broadcastId}`
    ).pipe(catchError(this.handleError));
  }
}
