import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { OperationsService } from 'src/app/shared/services/operations.service';
import { UserService } from 'src/app/shared/services/user.service';
import { Reason } from 'src/app/shared/models/reason.interface';
import { SessionsService } from 'src/app/shared/services/sessions.services';

@Component({
  selector: 'app-status-bar',
  templateUrl: './status-bar.component.html',
  styleUrls: ['./status-bar.component.css']
})
export class StatusBarComponent implements OnInit {

  @Output() close: EventEmitter<boolean> = new EventEmitter();
  @Output() reason: EventEmitter<string> = new EventEmitter();
  @Input() channelLicenses;
  @Input() open: boolean;

  pageName = 'Header';
  selectedReason: Reason = {
    externalId: '',
    name: 'UNAVAIABLE',
    operationId: this.config.JWT.OperationID,
    reason: 0,
    timeout: 0,
    userReasonId: 0,
  };
  customReasons: Reason[];
  defaultReasons: Reason[] = [
    {
      userReasonId: 0,
      operationId: this.config.JWT.OperationID,
      reason: 0,
      name: 'UNAVAIABLE',
      timeout: 0,
      externalId: ''
    }, {
      userReasonId: 0,
      operationId: this.config.JWT.OperationID,
      reason: 0,
      name: 'AVAIABLE',
      timeout: 0,
      externalId: ''
    },
  ];

  constructor(
    private config: ConfigService,
    private operationService: OperationsService,
    private userService: UserService,
    private sessionService: SessionsService) { }

  ngOnInit(): void {
    this.operationService.getReasons(this.config.JWT.OperationID).subscribe({
      next: res => this.customReasons = res,
      error: err => console.log(err)
    });

    this.sessionService.sessionChecker(this.config.JWT.UserId);
  }

  openSelectPhoneChannel(relationType) {

    this.config.PhoneChannelType = relationType;

    const modal = document.getElementById('modal-select-phone-channel');
    modal.classList.add('opened');
  }

  closeStatusBar() {
    this.close.emit(true);
  }

  changeReason(reason: Reason) {
    this.selectedReason = reason;
    this.userService.changeReason(reason);
    this.reason.emit(reason.name);
    this.closeStatusBar();
  }

  getCss(reasonName: string) {
    if (reasonName === 'AVAIABLE') {
      return ['status-online', 'animate-300', 'reason'];
    }
    if (reasonName === 'UNAVAIABLE') {
      return ['status-offline', 'animate-300', 'reason'];
    }
    return ['status-pause', 'animate-300', 'reason'];
  }
}
