import { Component } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from '../../../shared/services/config.service';

@Component({
  selector: 'app-relations',
  templateUrl: './relations.component.html',
  styleUrls: ['./relations.component.css']
})
export class RelationsComponent {

  pageName = 'Relations';

  constructor(config: ConfigService, public translate: TranslateService, titleService: Title) {

    const body = document.getElementsByTagName('body')[0];
    body.className = 'relationships-list-page';

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    titleService.setTitle(config.GetTranslationByPage(this.pageName, 'RelationsTitle'));
  }
}
