import { Component} from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ConfigService } from '../../shared/services/config.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent {

  pageName = 'Dashboard';

  constructor(config: ConfigService, public translate: TranslateService, titleService: Title) {

    const body = document.getElementsByTagName('body')[0];
    body.className = 'today-page';

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    // titleService.setTitle(config.GetTranslationByPage(this.pageName, 'DashboardTitle'));
  }
}
