import { Component, OnInit, Input } from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';

@Component({
  selector: 'app-channel-link',
  templateUrl: './channel-link.component.html',
  styleUrls: ['./channel-link.component.css']
})
export class ChannelLinkComponent {

  @Input() channelLicense;
  pageName = 'Header';

  constructor(public config: ConfigService) { }

  getIcon() {
    switch (this.channelLicense) {
      case 'phone-dial':
        return 'icon-dial-call';
      case 'phone-receive':
        return 'icon-receive-call';
      case 'chat':
        return 'icon-chat';
      case 'email':
        return 'icon-receive-email';
      case 'cases':
        return 'icon-receive-call';
      case 'sms':
        return 'icon-receive-call';
      case 'basic':
        return 'icon-receive-call';
      case 'digital-media':
        return 'icon-receive-call';
    }
  }

  getText() {
    switch (this.channelLicense) {
      case 'phone-dial':
        return 'lnkDial';
      case 'phone-receive':
        return 'lnkReceive';
      case 'chat':
        return 'lnkChat';
      case 'email':
        return 'lnkEmail';
      case 'cases':
        return 'lnkCases';
      case 'sms':
        return 'lnkSms';
      case 'basic':
        return 'lnkBasic';
      case 'digital-media':
        return 'lnkDigitalMedia';
    }
  }

  clickEvent() {
    switch (this.channelLicense) {
      case 'phone-dial':
        this.openSelectPhoneChannel(1);
        break;
      case 'phone-receive':
        this.openSelectPhoneChannel(2);
        break;
      case 'chat':
        break;
      case 'email':
        break;
      case 'cases':
        break;
      case 'sms':
        break;
      case 'basic':
        break;
      case 'desktop':
        break;
      case 'digital-media':
        break;
    }
  }

  openSelectPhoneChannel(relationType) {
    this.config.PhoneChannelType = relationType;

    const modal = document.getElementById('modal-select-phone-channel');
    modal.classList.add('opened');
  }

}
