import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { CustomerContactService } from 'src/app/shared/services/customer-contact.service';

@Component({
  selector: 'app-select-contact',
  templateUrl: './select-contact.component.html',
  styleUrls: ['./select-contact.component.css']
})
export class SelectContactComponent {
  pageName = 'SelectContact';
  Contacts;
  customer;

  @Input('Customer')
    set Customer(customer) {
      this.customer = customer;
      if (customer) {
        this.loadContacts(customer.Id);
      }
  }

  get Customer() {
    return this.customer;
  }

  @Output()
  messageEvent = new EventEmitter<any>();

  constructor(private contactService: CustomerContactService) { }

  loadContacts(customerId) {
    this.contactService.getContacts(customerId).subscribe(result => {
      if (result) {
        this.Contacts = result.contacts;
      }
    });
  }

  closeModal() {
    const modal = document.getElementById('modal-select-contact');
    modal.classList.remove('opened');
  }

  selectContact(contact) {
    this.messageEvent.emit(contact);
    this.closeModal();
  }

}
