import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { FormControlBase } from 'src/app/shared/models/reactive-forms/form-control-base';
import { ReactiveFormsService } from 'src/app/shared/services/reactive-forms.service';
import { TextboxControl } from 'src/app/shared/models/reactive-forms/textbox-control';
import { TextareaControl } from 'src/app/shared/models/reactive-forms/textarea-control';
import { DropdownControl } from 'src/app/shared/models/reactive-forms/dropdown-control';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { ConfigService } from 'src/app/shared/services/config.service';
import { CustomerCompanyService } from 'src/app/shared/services/customer-company.service';
import { Company } from 'src/app/shared/models/company.interface';

@Component({
  selector: 'app-company-quick-register',
  templateUrl: './company-quick-register.component.html',
  styleUrls: ['./company-quick-register.component.css']
})
export class CompanyQuickRegisterComponent implements OnInit {

  validator;
  controls: FormControlBase<any>[] = [];
  form: FormGroup;
  payLoad = '';
  pageName = 'CustomerCompany';

  errors;
  savingSuccessful = false;

  constructor(private rfs: ReactiveFormsService, private config: ConfigService, private customerCompanyService: CustomerCompanyService) { }

  ngOnInit() {
    this.getCompanyRequiredFields();
  }

  getCompanyRequiredFields() {
    this.customerCompanyService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.validator = new ValidationService(result.requiredFields);
        this.loadControls(this.validator.requiredFieldsList);
        this.form = this.rfs.toFormGroup(this.controls);
      }
    });
  }

  saveCompany({ value, valid }: { value: Company, valid: boolean }) {
    this.errors = '';
    this.savingSuccessful = false;

    if (valid) {

      value.CreatedBy = this.config.JWT.UserId;
      value.OperationId = this.config.JWT.OperationID;
      value.CustomerId = -1;
      value.AccountablePerson = -1;

      this.savingSuccessful = true;

      this.customerCompanyService.save(value).subscribe((result: any) => {
        if (result) {
          this.savingSuccessful = true;
        }
      },
        (error) => {
          this.savingSuccessful = false;
          this.errors = this.config.GetTranslationByPage(this.pageName, error);
        });

      const element = document.getElementsByClassName('form-router-modal')[1];
      element.scrollTop = 0;
    }
  }

  closeModal() {
    this.errors = '';
    this.savingSuccessful = false;
    const modal = document.getElementById('modal-company-quick-register');
    modal.classList.remove('opened');
    this.form.reset();
    this.form.patchValue({ Gender: '-1' });
    this.validator.invalidFields = [];
  }

  loadControls(fieldsList) {

    let controlOrder = 1;

    fieldsList.forEach(field => {

      let control;

      const controlLabel = this.config.GetTranslationByPage(this.pageName, 'lbl' + field);

      switch (field) {
        case 'Name':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'TradeName':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'Industry':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AnnualRevenue':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Money'
          });
          break;
        case 'Employees':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Number'
          });
          break;
        case 'Email':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Email'
          });
          break;
        case 'Email2':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Email'
          });
          break;
        case 'TaxId':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'TaxId'
          });
          break;
        case 'TaxId2':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'TaxId3':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'Phone':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Phone'
          });
          break;
        case 'Phone2':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Phone'
          });
          break;
        case 'Phone3':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Phone'
          });
          break;
        case 'Website':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'Facebook':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'PostalCode':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'PostalCode'
          });
          break;
        case 'AddressLine1':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AddressLine2':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AddressNumber':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AddressNumberComp':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'City':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'State':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'Country':
          control = new TextboxControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
        case 'AccountablePerson':
          control = new DropdownControl({
            key: field,
            label: controlLabel,
            value: '-1',
            options: [
              { key: '-1', value: this.config.GetTranslationByPage(this.pageName, 'ddoSelect') }
            ],
            order: controlOrder++
          });
          break;
        case 'Comments':
          control = new TextareaControl({
            key: field,
            label: controlLabel,
            required: true,
            order: controlOrder++,
            maskType: 'Text'
          });
          break;
      }

      if (control !== undefined) {
        this.controls.push(control);
      }
    });
  }

}
