import { timer } from 'rxjs';
import { Router } from '@angular/router';
import { Component, Output, EventEmitter, Input, OnInit } from '@angular/core';
import { UserService } from 'src/app/shared/services/user.service';
import { ConfigService } from 'src/app/shared/services/config.service';
import { RelationsService } from 'src/app/shared/services/relations.service';
import { OperationsService } from 'src/app/shared/services/operations.service';
import Operation from 'src/app/shared/models/operation';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.css']
})
export class UserProfileComponent implements OnInit {

  @Output() close: EventEmitter<boolean> = new EventEmitter();
  @Input() open: boolean;
  isChangeOperationEnabled = false;
  sumRelations = 0;
  sumTMA = 0;
  sumRelationPhoneCalls = 0;
  sumRelationEmails = 0;
  sumRelationChats = 0;
  sumRelationSelfAssistance = 0;
  sumRelationFax = 0;
  sumRelationLetter = 0;
  sumRelationSMS = 0;
  sumRelationBasic = 0;

  constructor(
    private configService: ConfigService,
    private userService: UserService,
    private operationService: OperationsService,
    private relationService: RelationsService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.loadOperations(this.configService.JWT.unique_name);
    this.getSummary();
  }

  closeUserProfile() {
    this.close.emit(true);
  }

  logout() {
    this.userService.logout(this.configService.JWT.OperationID).subscribe(result => {
      if (result) {
        window.location.href = '\login';
      }
    });
  }

  get userName() {
    return this.configService.JWT.unique_name;
  }

  get userId() {
    return this.configService.JWT.UserId;
  }

  get operationName() {
    return this.configService.JWT.OperationName;
  }

  loadOperations(userName: string) {
    this.operationService.listOperations(userName).subscribe({
      next: (res: Operation[]) => {
        if (res.length > 1) {
          this.isChangeOperationEnabled = true;
        }
      }
    });
  }

  getSummary() {
    timer(0, 180000).subscribe(x => {
      this.relationService.getSummaryByDate(
        this.configService.JWT.UserId,
        this.getTodayDate(),
        this.getTomorrowDate())
        .subscribe({
          next: res => {
            if (res.length <= 0) {
              return;
            }
            this.populateSummary(res[0]);
          },
          error: err => {
            console.log(err);
          }
        });
    });
  }

  populateSummary(summaries) {
    this.sumRelations = 0;
    this.sumRelationPhoneCalls = 0;
    this.sumRelationEmails = 0;
    this.sumRelationChats = 0;

    summaries.summaries.map(item => {

      if (item.relationType === 1 || item.relationType === 2) {
        this.sumRelationPhoneCalls += +item.sumRelation;
      }
      if (item.relationType === 3 || item.relationType === 4) {
        this.sumRelationEmails += +item.sumRelation;
      }
      if (item.relationType === 5) {
        this.sumRelationChats += +item.sumRelation;
      }
      if (item.relationType === 7 || item.relationType === 14) {
        this.sumRelationSelfAssistance += +item.sumRelation;
      }
      if (item.relationType === 8 || item.relationType === 9) {
        this.sumRelationFax += +item.sumRelation;
      }
      if (item.relationType === 10 || item.relationType === 11) {
        this.sumRelationLetter += +item.sumRelation;
      }
      if (item.relationType === 12 || item.relationType === 13) {
        this.sumRelationSMS += +item.sumRelation;
      }
      if (item.relationType === 15) {
        this.sumRelationBasic += +item.sumRelation;
      }
      this.sumRelations += +item.sumRelation;
      this.sumTMA += +item.avgDuration;
    });
  }

  convertTMA() {
    let hours: string | number = Math.floor(this.sumTMA / 60);
    let minutes: string | number = Math.floor((this.sumTMA - ((hours * 3600)) / 60));
    let seconds: string | number = Math.floor((this.sumTMA * 60) - (hours * 3600) - (minutes * 60));

    if (hours < 10) { hours = '0' + hours; }
    if (minutes < 10) { minutes = '0' + minutes; }
    if (seconds < 10) { seconds = '0' + seconds; }
    return hours + ':' + minutes + ':' + seconds;
  }

  getTodayDate() {
    const todayDate = new Date().toLocaleDateString();
    return this.formatDate(todayDate);
  }

  getTomorrowDate() {
    const tomorrowDate = new Date();
    tomorrowDate.setDate(tomorrowDate.getDate() + 1);

    return this.formatDate(tomorrowDate.toLocaleDateString());
  }

  formatDate(date: string) {
    const day = date.split('/')[0];
    const month = date.split('/')[1];
    const year = date.split('/')[2];

    return year + '-' + ('0' + month).slice(-2) + '-' + ('0' + day).slice(-2);
  }

  changeOperation() {
    this.userService.changeOperation(this.configService.JWT.OperationID).subscribe(result => {
      if (result) {
        this.router.navigate(['/select-operation']);
      }
    });
  }
}
