import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ConfigService } from './config.service';
import { BaseService } from './base.service';
import { map, catchError } from 'rxjs/operators';
import { Person } from '../models/person.interface';

@Injectable()
export class CustomerPersonService extends BaseService {

  constructor(private httpClient: HttpClient, private config: ConfigService) {
    super();
  }

  getPerson(customerId: number) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/persons/${customerId}`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  save(person: Person) {

    const body = JSON.stringify(person);

    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/persons`, body )
   .pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getCompanies(customerId: number) {

    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/persons/${customerId}/companies`
    ).pipe(
      map(result => {
        if (result) {
          return result;
        }
      }),
      catchError(this.handleError));
  }

  getRequiredFields(operationId: number) {

    return this.httpClient.get<any>(
        `${this.config.BackendUri}/api/operations/${operationId}/person-required-fields`
    ).pipe(
        map(result => {
        if (result) {
            return result;
        }
        }),
        catchError(this.handleError));
  }
}
