import { Injectable } from '@angular/core';
import { BaseService } from './base.service';
import { HttpClient, HttpParams } from '@angular/common/http';
import { ConfigService } from './config.service';
import { catchError } from 'rxjs/operators';

@Injectable()
export class CasesService extends BaseService {

    constructor(private httpClient: HttpClient, private config: ConfigService) {
        super();
    }

    getDueByUser(userId: number, orderBy: string, ascDesc: string, culture: string, pageIndex: number, pageSize: number) {
        const params = new HttpParams().set('userId', userId.toString())
            .set('orderBy', orderBy.toString())
            .set('ascDesc', ascDesc)
            .set('culture', culture)
            .set('pageIndex', pageIndex.toString())
            .set('pageSize', pageSize.toString());

        return this.httpClient.get<any>(
            `${this.config.BackendUri}/api/cases/due-by-user`, { params }
        ).pipe(catchError(this.handleError));
    }

    getOpenByUser(userId: number, orderBy: string, ascDesc: string, culture: string, pageIndex: number, pageSize: number) {
        const params = new HttpParams().set('userId', userId.toString())
            .set('orderBy', orderBy.toString())
            .set('ascDesc', ascDesc)
            .set('culture', culture)
            .set('pageIndex', pageIndex.toString())
            .set('pageSize', pageSize.toString());

        return this.httpClient.get<any>(
            `${this.config.BackendUri}/api/cases/opened-by-user`, { params }
        ).pipe(catchError(this.handleError));
    }

    getClosedByUser(
        userId: number,
        dateStart: string,
        dateFinish: string,
        orderBy: string,
        ascDesc: string,
        culture: string,
        pageIndex: number,
        pageSize: number
    ) {
        const params = new HttpParams().set('userId', userId.toString())
            .set('dateStart', dateStart)
            .set('dateFinish', dateFinish)
            .set('orderBy', orderBy.toString())
            .set('ascDesc', ascDesc)
            .set('culture', culture)
            .set('pageIndex', pageIndex.toString())
            .set('pageSize', pageSize.toString());

        return this.httpClient.get<any>(
            `${this.config.BackendUri}/api/cases/closed-by-user`, { params }
        ).pipe(catchError(this.handleError));
    }

    getSummaryInfo(userId: number) {
        const params = new HttpParams().set('userId', userId.toString());

        return this.httpClient.get<any>(
            `${this.config.BackendUri}/api/casessummary/summary-by-user`, { params }
        ).pipe(catchError(this.handleError));
    }
}
