import { Control } from '../models/control.interface';
import { CustomerType } from '../models/customer-type.enum';

export class ValidationService {

    // Field Validation
    public invalidFields = [];
    public requiredFieldsList = [];

    // Validation Expressions
    public dynamicMask: string;
    public phoneMask: string;
    public taxIdMask: string;
    public postalCodeMask: string;
    public phonePatternRegEx: string;
    public taxIdPatternRegEx: string;
    public emailPatternRegEx = '[\\w-]+(\\.[\\w-]+)*@\\w+([-.]\\w+)*\\.\\w+([-.]\\w+)*';

    constructor(public requiredFields?) {

        if (requiredFields) {
            this.getRequiredFields(this.requiredFields);
            this.getRequiredFieldsList();
        }
    }

    getRequiredFields(requiredFields) {

        this.requiredFields = requiredFields;

        if (this.requiredFields.ValidationRules) {
            this.taxIdPatternRegEx = this.requiredFields.ValidationRules.TaxIdRegEx;
            this.phonePatternRegEx = this.requiredFields.ValidationRules.PhoneRegEx;
            this.phoneMask = this.requiredFields.ValidationRules.PhoneMask;
            this.taxIdMask = this.requiredFields.ValidationRules.TaxIdMask;
            this.postalCodeMask = this.requiredFields.ValidationRules.PostalCodeMask;
            this.setDynamicMask();
        }
    }

    getRequiredFieldsList() {

        const fieldsKeys = Object.keys(this.requiredFields);

        fieldsKeys.forEach(field => {
            if (this.requiredFields[field] === true) {
                this.requiredFieldsList.push(field);
            }
        });
    }

    addRequiredField(fieldName) {
        this.requiredFieldsList.push(fieldName);
    }

    checkRequiredField(fieldName: string): Boolean {
        if (this.requiredFieldsList !== undefined) {
            return this.requiredFieldsList.includes(fieldName);
        }
        return false;
    }

    requiredValidation(fieldName, value) {

        const isRequired = this.checkRequiredField(fieldName);
        const isValid = !(isRequired && (value === '' || (value === '-1')));
        return this.validateField(fieldName, isValid);
    }

    fieldValidation(type, control) {

        let isValid = true;
        const isRequired = this.checkRequiredField(control.name);
        let pattern;
        let value = control.value;

        switch (type) {
            case 'Text':
                pattern = '';
                break;
            case 'Email':
                pattern = this.emailPatternRegEx;
                break;
            case 'Phone':
                pattern = this.phonePatternRegEx;
                value = control.value.replace(/[^\w\s]/gi, '');
                break;
            case 'TaxId':
            case 'CPF':
            case 'CNPJ':
                pattern = this.taxIdPatternRegEx;
                break;
        }

        if (isRequired && (!value || value === '' || value === '-1' || value === -1)) {
            isValid = false;
        } else if (value !== '') {
            const regEx = new RegExp(pattern);
            isValid = regEx.test(value);

            if (isValid) {
                if (type === 'CPF') {
                    isValid = this.validateCpf(value);
                } else if (type === 'CNPJ') {
                    isValid = this.validateCnpj(value);
                }
            }
        }

        this.validateField(control.name, isValid);
    }

    validateField(controlName, isValid) {
        if (!isValid) {
            this.addInvalid(controlName);
            return false;
        } else {
            this.removeInvalid(controlName);
            return true;
        }
    }

    checkInvalidField(field) {
        return this.invalidFields.includes(field);
    }

    addInvalid(controlName) {
        this.invalidFields.push(controlName);
    }

    removeInvalid(controlName) {
        this.invalidFields = this.invalidFields.filter(i => i !== controlName);
    }

    setDynamicMask() {
        const newMask = this.phoneMask.replace('-', '');
        const dashIndex = this.phoneMask.length - 5;
        this.dynamicMask = newMask.slice(0, dashIndex) + '-' + newMask.slice(dashIndex, newMask.length);
    }

    dynamicPhone(value) {

        const index = value.indexOf('-');

        let dashIndex = 0;
        if (index > 0) {
            dashIndex = value.length - value.indexOf('-');
        }

        if (dashIndex > 5) {
            // e.g '(00) 00000-0000'
            this.phoneMask = this.dynamicMask;
        } else if (value.length < this.phoneMask.length) {
            // e.g '(00) 0000-00000'
            this.phoneMask = this.requiredFields.ValidationRules.PhoneMask;
        }
    }

    getPhoneMask(phoneNumber): string {

        if (phoneNumber !==  undefined && phoneNumber !== '') {

            const trimmedPhone = phoneNumber.replace(' ', '');

            if (trimmedPhone.length < 11) {
                const dashIndex = this.phoneMask.indexOf('-');
                let newMask = this.phoneMask.replace('-', '');
                newMask = newMask.slice(0, dashIndex - 1) + '-' + newMask.slice(dashIndex, newMask.length);
                return newMask;
            }
        }

        return this.phoneMask;
    }

    getMask(maskType) {
        switch (maskType) {
            case 'TaxId':
                return this.taxIdMask;
            case 'Phone':
                return this.phoneMask;
            case 'PostalCode':
                return this.postalCodeMask;
            case 'Number':
                return 'dot_separator';
            case 'Money':
                return 'dot_separator.2';
            default:
                return '';
        }
    }

    validateCpf(cpf: string) {
        const exp = /\.|\-/g;
        cpf = cpf.replace(exp, '');
        const digitoDigitado = +(cpf.charAt(9) + cpf.charAt(10));
        let soma1 = 0;
        let soma2 = 0;
        let vlr = 11;

        for (let i = 0; i < 9; i++) {
            soma1 += +cpf.charAt(i) * (vlr - 1);
            soma2 += +cpf.charAt(i) * vlr;
            vlr--;
        }

        soma1 = (soma1 * 10) % 11 === 10 ? 0 : (soma1 * 10) % 11;
        soma2 = ((soma2 + (2 * soma1)) * 10) % 11;

        const digitoGerado = (soma1 * 10) + soma2;

        return digitoGerado === digitoDigitado;
    }

    validateCnpj(cnpj: string) {

        const valida = new Array(6, 5, 4, 3, 2, 9, 8, 7, 6, 5, 4, 3, 2);
        let dig1 = 0;
        let dig2 = 0;

        const exp = /\.|\-|\//g;
        cnpj = cnpj.toString().replace(exp, '');
        const digito = +(cnpj.charAt(12) + cnpj.charAt(13));

        for (let i = 0; i < valida.length; i++) {
            dig1 += i > 0 ? +cnpj.charAt(i - 1) * valida[i] : 0;
            dig2 += +cnpj.charAt(i) * valida[i];
        }

        dig1 = (dig1 % 11) < 2 ? 0 : 11 - (dig1 % 11);
        dig2 = (dig2 % 11) < 2 ? 0 : 11 - (dig2 % 11);

        const digitoGerado = (dig1 * 10) + dig2;

        return digitoGerado === digito;
    }

    customValidateSave(formValue) {

        this.requiredFieldsList.forEach(field => {

            const control = new Control();
            control.name = field;
            control.value = formValue[field];

            this.fieldValidation('Text', control);
        });

        return this.invalidFields.length === 0;
    }

    validateSave(customerType: CustomerType, formValue): boolean {

        this.requiredFieldsList.forEach(field => {

            let type = 'Text';

            switch (field) {
            case 'Email':
            case 'Email2':
                type = 'Email';
                break;
            case 'TaxId':
                if (customerType === CustomerType.Person) {
                    type = 'CPF';
                } else if (customerType === CustomerType.Company) {
                    type = 'CNPJ';
                }
                break;
            case 'Phone':
            case 'Phone2':
            case 'Phone3':
            case 'Mobile':
                type = 'Phone';
                break;
            }

            const control = new Control();
            control.name = field;
            control.value = formValue[field];

            this.fieldValidation(type, control);
        });

        return this.invalidFields.length === 0;
    }
}

