import {Component, Input, Output, EventEmitter} from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';

@Component ({
  selector: 'app-tree-view',
  styleUrls: ['./tree-view.component.css'],
  templateUrl: './tree-view.component.html',
})

export class TreeViewComponent {

  @Input() Parent;
  @Input() Subjects;

  @Output() messageEvent = new EventEmitter();

  constructor(private config: ConfigService, private util: UtilitiesService) {
  }

  getClass() {
    if (!this.Parent) {
      return 'opened categoria';
    } else if (this.hasChildrenCheck()) {
      return 'opened subcategoria';
    } else {
      return 'opcao';
    }
  }

  hasChildrenCheck(): boolean {

    if (this.Subjects && this.Subjects.length > 0) {
      return this.Subjects[0].HasChildren;
    }
    return false;
  }

  toogleClass(subjectId) {
    this.util.toggleSubjectView(document, subjectId);
  }

  selectSubject(subject) {

    const subjectItems = this.config.RelationSubjectItems;

    if (!subjectItems.find(i => i.Id === subject.Id)) {
      subjectItems.push(subject);
      this.config.RelationSubjectItems = subjectItems;
    }

    this.messageEvent.emit();
  }

  receiveEvent() {
    this.messageEvent.emit();
  }

  toggleSubjectItemCheck(subject) {

    let subjectItems = this.config.RelationSubjectItems;

    if (subjectItems.find(i => i.Id === subject.Id)) {
      subjectItems = subjectItems.filter(i => i.Id !== subject.Id);
    } else {
      subjectItems.push(subject);
    }

    this.config.RelationSubjectItems = subjectItems;
  }

  itemIsChecked(subjectId) {
    return this.config.RelationSubjectItems.find(i => i.Id === subjectId);
  }
}
