import { Title } from '@angular/platform-browser';
import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from '../../../shared/services/config.service';
import { CustomersService } from '../../../shared/services/customers.service';

import { CustomerSearch } from '../../../shared/models/customer-search.interface';
import { finalize } from 'rxjs/operators';
import { CustomerSearchType } from 'src/app/shared/models/customer-search-type.interface';
import { ColumnCustomer } from 'src/app/shared/models/columnCustomer.class';
import { IColumn } from 'src/app/shared/models/IColumn';
import { ColumnType } from 'src/app/shared/models/columnType.enum';

@Component({
  selector: 'app-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.css']
})

export class CustomersComponent implements OnInit {

  pageName = 'Customers';
  errors: string;
  customersCount = 0;
  SearchTypeId = '0';
  SearchValue: string;
  SearchTypes: CustomerSearchType[];
  isLoading = false;
  isLoadingSearchTypes = true;
  sortDirection: string;
  orderBy: string;
  searchType: '';
  searchValue: '';

  dataSource: any = [];
  displayedColumns: IColumn[] = [];

  constructor(
    public config: ConfigService,
    public translate: TranslateService,
    public titleService: Title,
    private customersService: CustomersService
  ) {
    const body = document.getElementsByTagName('body')[0];
    body.className = 'clients-list-page';
  }

  ngOnInit() {
    this.translate.getTranslation(this.config.Culture);
    this.translate.use(this.config.Culture);
    this.titleService.setTitle(this.config.GetTranslationByPage(this.pageName, 'ClientsTitle'));
    this.loadSearchTypes();
    this.getDisplayedColumns();
  }

  loadSearchTypes() {
    this.isLoadingSearchTypes = true;
    this.customersService.getSearchTypes().subscribe((searchTypes: CustomerSearchType[]) => {
      searchTypes.map((searchType: CustomerSearchType) => {
        if (searchType.type === 0) {
          searchType.name = this.config.GetTranslationByPage(this.pageName, searchType.id);
          searchType.id = 'orb_' + searchType.id;
        } else if (searchType.type === 1) {
          searchType.id = "cp_" + searchType.id;
        } else {
          searchType.id = "cpCtrl_" + searchType.id;
        }
      });
      this.SearchTypes = searchTypes;
      this.isLoadingSearchTypes = false;
    });
  }

  search({ value, valid }: { value: CustomerSearch, valid: boolean }) {
    if (valid) {
      this.doSearch(value.SearchTypeId, value.SearchValue);
    }
  }

  doSearch(searchType, searchValue) {
    this.searchType = searchType;
    this.searchValue = searchValue;
    this.isLoading = true;
    const params = new CustomerSearch();
    params.UserId = this.config.JWT.UserId;
    params.OperationId = this.config.JWT.OperationID;
    params.SearchValue = searchValue;
    params.PageIndex = 0;
    params.PageSize = 100;
    params.SortDirection = this.sortDirection;
    params.OrderBy = this.orderBy;
    if (searchValue === '' || searchValue === undefined) {
      this.isLoading = false;
      return
    } else {
      params.SearchTypeId = searchType;
    }

    this.customersService.search(params).pipe(
      finalize(() => {
        this.finalizeLoading();
      })).subscribe(result => {
        if (result) {
          this.dataSource = result.searchResult
        }
      },
        error => {
          this.errors = this.config.GetTranslationByPage(this.pageName, error);
          this.finalizeLoading();
        }
      );
  }

  getDisplayedColumns() {
    this.customersService.getSearchColumns().subscribe((result: IColumn[]) => {
      const customerColumns: IColumn[] = [];
      result.map(column => {
        if (column.isIcon) {
          customerColumns.push(new ColumnCustomer(column.columnName, column.customerProviderType, column.initialVisible, column.isClickable, column.isIcon, column.isSortable, ColumnType.Icon))
        } else if (column.columnName === 'details') {
          customerColumns.push(new ColumnCustomer(column.columnName, column.customerProviderType, column.initialVisible, column.isClickable, column.isIcon, column.isSortable, ColumnType.Detail))
        } else {
          customerColumns.push(new ColumnCustomer(column.columnName, column.customerProviderType, column.initialVisible, column.isClickable, column.isIcon, column.isSortable, ColumnType.Row))
        }
      })
      this.displayedColumns = customerColumns;
    })
  }

  finalizeLoading() {
    const boxLogin = document.querySelector('.clients-list');
    boxLogin.classList.remove('loading');
    this.isLoading = false;
  }

  openQuickRegister(type) {
    const modal = document.getElementById('modal-' + type + '-quick-register');
    modal.classList.add('opened');
  }

  orderData(event) {
    if (this.searchType && this.searchValue && this.dataSource.length > 1) {
      this.sortDirection = event.direction;
      this.orderBy = event.active;
      this.doSearch(this.searchType, this.searchValue);
    }
  }
}
