import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { FormControlBase } from '../models/reactive-forms/form-control-base';

@Injectable()
export class ReactiveFormsService {
  constructor() { }

  toFormGroup(components: FormControlBase<any>[] ) {
    const group: any = {};

    components.forEach(component => {
      group[component.key] = component.required ? new FormControl(component.value || '',
                                                  { validators: Validators.required,  updateOn: 'blur' })
                                                : new FormControl(component.value || '', { updateOn: 'blur' });
    });
    return new FormGroup(group);
  }
}
