import { Component } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ConfigService } from '../../shared/services/config.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-cases',
  templateUrl: './cases.component.html',
  styleUrls: ['./cases.component.css']
})
export class CasesComponent {

  pageName = 'Cases';

  constructor(config: ConfigService, public translate: TranslateService, titleService: Title) {

    const body = document.getElementsByTagName('body')[0];
    body.className = 'cases-list-page';

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    titleService.setTitle(config.GetTranslationByPage(this.pageName, 'CasesTitle'));
  }
}
