import { Title } from '@angular/platform-browser';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';
import { ValidationService } from 'src/app/shared/services/validation.service';
import { CustomerPersonService } from 'src/app/shared/services/customer-person.service';
import { HistoryCollection } from 'src/app/shared/models/history-collection.interface';
import { CustomersService } from 'src/app/shared/services/customers.service';

@Component({
  selector: 'app-person-view',
  templateUrl: './person-view.component.html',
  styleUrls: ['./person-view.component.css']
})

export class PersonViewComponent implements OnInit {

  pageName = 'PersonView';
  fullName;
  fullAddress;
  age: number;
  cityState: string;
  birthdayDate;
  formattedBirthDate;
  genderText;
  isBirthdayDate: boolean;
  validation;
  contacts;
  history = [];
  historiesCollection: HistoryCollection[];
  pageIndex = 0;
  Period = 30;
  periods = [30, 60, 90];
  filter = 'all';

  // Personal Data
  CustomerId = -1;
  ContactId = 0;
  FirstName: string;
  MiddleName: string;
  LastName: string;
  TaxId: string;
  TaxId2: string;
  Gender = -1;
  Email: string;
  Email2: string;
  Phone: string;
  Phone2: string;
  Phone3: string;
  Mobile: string;
  Nickname: string;
  BirthDate: Date;
  Website: string;
  Facebook: string;
  Twitter: string;
  Comment: string;
  AccountablePerson = -1;
  Family = -1;

  // Address
  PostalCode: string;
  AddressLine1: string;
  AddressLine2: string;
  AddressNumber: string;
  AddressNumberComp: string;
  City: string;
  State: string;
  Country: string;

  constructor(private router: Router, private config: ConfigService, public util: UtilitiesService, public translate: TranslateService,
    titleService: Title, private activatedRoute: ActivatedRoute, private customersService: CustomersService,
    private customerPersonService: CustomerPersonService) {

    translate.getTranslation(config.Culture);
    translate.use(config.Culture);

    titleService.setTitle(config.GetTranslationByPage(this.pageName, 'PersonViewTitle'));

    this.getPersonRequiredFields();

    util.changeBody('clients-list-page');
  }

  ngOnInit() {

    if (this.router.url.includes('customer')) {
      const clientLink = document.querySelector('.clients-link');
      clientLink.classList.add('active');
    }

    this.activatedRoute.queryParams.subscribe((params) => {
      if (params['customerId']) {
        this.CustomerId = +params['customerId'];

        if (params['contactId']) {
          this.ContactId = +params['contactId'];
        }

        this.loadPerson(this.CustomerId);
        this.loadCompanies(this.CustomerId);
        this.loadHistory();
      }
    });
  }

  getPersonRequiredFields() {
    this.customerPersonService.getRequiredFields(this.config.JWT.OperationID).subscribe(result => {
      if (result) {
        this.validation = new ValidationService(result.requiredFields);
      }
    });
  }

  getFormattedBirthDate(): string {
    return this.birthdayDate.toLocaleDateString();
  }

  getAge(): number {

    const timeDiff = Math.abs(Date.now() - this.birthdayDate.getTime());
    const age = Math.floor((timeDiff / (1000 * 3600 * 24)) / 365.25);

    return age;
  }

  getFullAddress(): string {

    let fullAdress = this.AddressLine1;

    if (this.AddressNumber !== null && this.AddressNumber !== '') {
      fullAdress += ', ' + this.AddressNumber;
    }

    return fullAdress;
  }

  getCityState(): string {
    let cityState = this.City;

    if (this.State !== '') {
      cityState += '/' + this.State;
    }

    return cityState;
  }

  getGender(): string {
    switch (this.Gender) {
      case 0:
        return this.config.GetTranslationByPage(this.pageName, 'lblMale');
      case 1:
        return this.config.GetTranslationByPage(this.pageName, 'lblFemale');
    }
  }

  loadPerson(customerId: number) {
    this.customerPersonService.getPerson(customerId).subscribe(result => {
      const person = result.person;

      // Personal
      this.FirstName = person.FirstName;
      this.MiddleName = person.MiddleName;
      this.LastName = person.LastName;
      this.TaxId = person.TaxId;
      this.TaxId2 = person.TaxId2;
      this.Gender = person.Gender;
      this.Email = person.Email;
      this.Email2 = person.Email2;
      this.Phone = person.Phone;
      this.Phone2 = person.Phone2;
      this.Phone3 = person.Phone3;
      this.Mobile = person.Mobile;
      this.Nickname = person.Nickname;
      this.BirthDate = person.BirthDate; // '06/06/1981 00:00:00';
      this.Website = person.Website;
      this.Facebook = person.Facebook;
      this.Twitter = person.Twitter;
      this.Comment = person.Comment;
      this.AccountablePerson = person.AccountablePerson;

      // Address
      this.PostalCode = person.PostalCode;
      this.AddressLine1 = person.AddressLine1;
      this.AddressLine2 = person.AddressLine2;
      this.AddressNumber = person.AddressNumber;
      this.AddressNumberComp = person.AddressNumberComp;
      this.City = person.City;
      this.State = person.State;
      this.Country = person.Country;

      this.fullName = this.util.getFullName(person.FirstName, person.MiddleName, person.LastName);
      this.birthdayDate = new Date(this.BirthDate);
      this.age = this.util.calculateAge(person.BirthDate);
      this.isBirthdayDate = this.util.birthdayCheck(this.BirthDate);
      this.fullAddress = this.getFullAddress();
      this.cityState = this.getCityState();
      this.formattedBirthDate = this.getFormattedBirthDate();
      this.genderText = this.getGender();
    });
  }

  loadCompanies(customerId: number) {
    this.customerPersonService.getCompanies(customerId).subscribe(result => {
      this.contacts = result.contacts;
    });
  }

  loadHistory() {

    this.customersService.getHistory(this.CustomerId, this.ContactId, this.config.JWT.UserId, this.Period,
      this.pageIndex, this.filter).subscribe(result => {

        if (result.history.length !== 0) {
          result.history.forEach(history => {
            this.history.push(history);
          });
          this.buildHistoryCollection(this.history);
        }
      });
  }

  buildHistoryCollection(history) {
    let dates = history.map(h => this.util.convertMonthYear(h.Date, this.config.Culture));
    dates = dates.filter((item, index) => dates.indexOf(item) === index);

    this.historiesCollection = new Array();

    dates.forEach(date => {
      const histories = history.filter(f => this.util.convertMonthYear(f.Date, this.config.Culture) === date);
      const historyCollection = new HistoryCollection();
      historyCollection.MonthYear = date;
      historyCollection.Histories = histories;

      this.historiesCollection.push(historyCollection);
    });
  }

  getFormattedDate(date: Date): string {
    return this.util.getFormattedDate(date, this.config.GetTranslationByPage(this.pageName, 'lblAt'));
  }

  loadMoreHistory() {
    this.pageIndex++;
    this.loadHistory();
  }

  changePeriod() {
    this.pageIndex = 0;
    this.history = [];
    this.historiesCollection = [];
    this.loadHistory();
  }

  changeFilter(filter) {
    this.filter = filter;
    this.changePeriod();
  }
}
