import { Injectable } from '@angular/core';
import { Credentials } from '../models/credentials.interface';
import * as jwt_decode from 'jwt-decode';
import * as data from '../../../assets/config/config.json';

@Injectable({
  providedIn: 'root'
})

export class ConfigService {

  InterfaceName = 'AngularDesktop';

  constructor() { }

  public get BackendUri(): string {
    return data.config.BackendUri;
  }

  public GetTranslationByPage(page: string, control: string) {
    const translationSession = JSON.parse(localStorage.getItem('TranslationObject'));
    const result = translationSession[this.InterfaceName][page][control];
    if (result === undefined) {
      return control;
    }
    return result;
  }

  public get IsLoggedIn(): boolean {
    return !!sessionStorage.getItem('AuthToken');
  }

  public SetCredentials(value: Credentials) {

    let credentials: Array<Credentials> = JSON.parse(sessionStorage.getItem('Credentials'));

    if (credentials === null) {
      credentials = new Array<Credentials>();
    }

    credentials = credentials.filter(f => f.username !== value.username);
    credentials.push(value);

    sessionStorage.setItem('Credentials', JSON.stringify(credentials));
  }

  public GetCredentials(username: string): Credentials {

    if (this.JWT.unique_name !== null) {
      const credentials = JSON.parse(sessionStorage.getItem('Credentials'));
      const credential = credentials.find(f => f.username === username);
      return credential;
    }

    return undefined;
  }

  public set PhoneChannelType(value: number) { sessionStorage.setItem('PhoneChannelType', value.toString()); }
  public get PhoneChannelType(): number {
    const value = sessionStorage.getItem('PhoneChannelType');
    if (value == null) {
      return 0;
    } else {
      return +value;
    }
  }

  public get CtiMaxTimeout(): number {
    return data.config.CTI.MaxTimeout / data.config.CTI.RequestInterval;
  }

  public get CtiRequestInterval(): number {
    const config: any = JSON.parse(sessionStorage.getItem('Config'));
    return config.CTI.RequestInterval * 1000;
  }

  public set Culture(value: string) { sessionStorage.setItem('Culture', value); }
  public get Culture(): string {
    const value = sessionStorage.getItem('Culture');
    if (value == null) {
      return 'pt-br';
    } else {
      return value;
    }
  }

  public set IdentityId(value: string) { sessionStorage.setItem('IdentityId', value.toString()); }
  public get IdentityId(): string {
    const value = sessionStorage.getItem('IdentityId');
    if (value == null) {
      return '';
    } else {
      return value;
    }
  }

  public set PageIndex(value: number) { sessionStorage.setItem('PageIndex', value.toString()); }
  public get PageIndex(): number {
    const value = +sessionStorage.getItem('PageIndex');
    if (value == null) {
      return 0;
    } else {
      return value;
    }
  }

  public get CustomerPageSize(): number {
    return data.config.Customers.PageSize;
  }

  public set PhoneChannel(value: any) { sessionStorage.setItem('PhoneChannel', JSON.stringify(value)); }
  public get PhoneChannel(): any {
    const value = JSON.parse(sessionStorage.getItem('PhoneChannel'));
    return value;
  }

  public set RelationSubjectItems(value: any) { sessionStorage.setItem('RelationSubjectItems', JSON.stringify(value)); }
  public get RelationSubjectItems(): any {
    const value = JSON.parse(sessionStorage.getItem('RelationSubjectItems'));
    return value ? value : [];
  }

  public set RelationAllSubjects(value: any) { sessionStorage.setItem('RelationAllSubjects', JSON.stringify(value)); }
  public get RelationAllSubjects(): any {
    const value = JSON.parse(sessionStorage.getItem('RelationAllSubjects'));
    return value ? value : [];
  }

  public set JWT(value: any) {
    sessionStorage.setItem('AuthToken', value);
  }
  public get JWT(): any {
    const JWT: {} = sessionStorage.getItem('AuthToken');
    return jwt_decode(JWT);
  }
}


