import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { ConfigService } from 'src/app/shared/services/config.service';
import { TranslateService } from '@ngx-translate/core';
import { CustomerInfo } from 'src/app/shared/models/customer-info.interface';

@Component({
  selector: 'app-select-customer',
  templateUrl: './select-customer.component.html',
  styleUrls: ['./select-customer.component.css']
})
export class SelectCustomerComponent implements OnInit {

  pageName = 'SelectCustomer';
  anonymousCustomer = new CustomerInfo();

  @Output() messageEvent = new EventEmitter<string>();

  constructor(public config: ConfigService, public translate: TranslateService) {
    this.anonymousCustomer.Id = -1;
    this.anonymousCustomer.Name = 'Cliente Anônimo';
  }

  ngOnInit() {
  }

  closeModal() {
    const modal = document.getElementById('modal-select-customer');
    modal.classList.remove('opened');
  }

  receiveMessage($event) {
    this.messageEvent.emit($event);
    this.closeModal();
  }

  selectAnonymous(customer) {
    this.messageEvent.emit(customer);
    this.closeModal();
  }
}
