import { Component, OnInit, AfterViewInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfigService } from '../../shared/services/config.service';
import { OperationsService } from '../../shared/services/operations.service';
import { UtilitiesService } from 'src/app/shared/services/utilities.service';
import { IdleService } from 'src/app/shared/services/idle.service';
import { SessionsService } from 'src/app/shared/services/sessions.services';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit, AfterViewInit {

  pageName = 'Header';
  labels = {
    articleLabel: '',
    caseLabel: '',
    customerLabel: '',
    relationLabel: '',
  };
  logo = '';
  isUserProfileOpen = false;
  isTaskBarOpen = false;
  isStatusBarOpen = false;
  showPhoneButton = true;
  status = 'offline';
  channelLicenses;

  constructor(
    private config: ConfigService,
    public translate: TranslateService,
    private operationsService: OperationsService,
    private idleService: IdleService,
    private util: UtilitiesService,
    private sessionService: SessionsService) { }

  ngAfterViewInit(): void {
    this.idleService.checkIdle();
  }

  ngOnInit() {
    this.translate.getTranslation(this.config.Culture);
    this.translate.use(this.config.Culture);
    this.sessionService.statusChanger.subscribe((data: any) => {
      this.changeStatusColor(data.sessions[0].reasonName);
    });
    const operationId = this.config.JWT.OperationID;

    this.operationsService.getLabels(operationId).subscribe(data => {
      this.labels = data;
    });
    this.operationsService.getLogo(this.config.JWT.OperationID).subscribe(data => {
      this.logo = data;
    });
  }

  toggleStatus() {
    const modal = document.getElementById('modal-status');
    modal.classList.toggle('opened');
  }

  toggleUserProfile() {
    this.isUserProfileOpen = !this.isUserProfileOpen;
    this.showPhoneButton = !this.showPhoneButton;
  }

  toggleUserTasks() {
    this.isTaskBarOpen = !this.isTaskBarOpen;
    this.showPhoneButton = !this.showPhoneButton;
  }

  toggleStatusBar() {
    this.isStatusBarOpen = !this.isStatusBarOpen;
    this.channelLicenses = this.util.paginate(this.sessionService.getChannelLicenses(this.config.JWT.UserId), 4);
  }

  changeStatusColor(status: string) {
    if (status === 'Disponível' || status === 'AVAIABLE') {
      this.status = 'online';
      return;
    }
    if (status === 'Indisponível' || status === null || status === 'UNAVAIABLE') {
      this.status = 'offline';
      return;
    }
    this.status = 'pause';
  }
}
