import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { ConfigService } from './config.service';
import { BaseService } from './base.service';
import { map, catchError } from 'rxjs/operators';
import * as CryptoJS from 'crypto-js';
import { CTILogin } from '../models/cti-login.interface';
import { Credentials } from '../models/credentials.interface';
import { Reason } from '../models/reason.interface';
import { Observable } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class UserService extends BaseService {

  ctiPermissions;

  constructor(private config: ConfigService, private httpClient: HttpClient) {
    super();
  }

  login(username: string, password: string) {

    password = this.encryptPassword(password);
    const body = JSON.stringify({ username, password });

    return this.httpClient.post<any>(`${this.config.BackendUri}/api/logins/user-login`, body
    ).pipe(
      map((credentials: Credentials) => {
        if (credentials && credentials.token) {
          sessionStorage.setItem('AuthToken', credentials.token);
          return true;
        }
      }),
      catchError(this.handleError));
  }

  refreshTokenLogin(credentials: Credentials) {

    return this.httpClient.post<any>(`${this.config.BackendUri}/api/logins/refresh-token-login`, credentials
    ).pipe(
      map((result: Credentials) => {
        if (result && result.token) {
          sessionStorage.setItem('AuthToken', result.token);
          this.config.JWT.unique_name = result.username;
          return true;
        }
      }),
      catchError(this.handleError));
  }

  logout(operationId: number) {

    return this.httpClient.post<any>(`${this.config.BackendUri}/api/logins/user-logout/${operationId}`, null)
      .pipe(
        map(result => {
          sessionStorage.clear();
          return true;
        }),
        catchError(this.handleError));
  }

  recoverPassword(username: string, token: string) {

    const params = new HttpParams().set('username', username).set('recaptchaToken', token).set('culture', this.config.Culture);

    return this.httpClient.post<any>(
      this.config.BackendUri + 'api/users/recovery-password', { params })
      .pipe(
        map(result => {
          if (result) {
            return result;
          }
        }),
        catchError(this.handleError));
  }

  listCtiPermissions(userName: string) {
    return this.httpClient.get<any>(`${this.config.BackendUri}/api/users/${userName}/cti-permissions`)
      .pipe(
        map(result => {
          if (result) {
            this.ctiPermissions = result;
            return result;
          }
        }),
        catchError(this.handleError));
  }

  getCtiInfo(userName: string, operationId: Number) {
    const params = new HttpParams().set('operationId', operationId.toString());

    return this.httpClient.get<CTILogin>(`${this.config.BackendUri}/api/users/${userName}/cti-info`, { params })
      .pipe(
        catchError(this.handleError)
      );
  }

  encryptPassword(password: string): string {
    const keyCode = CryptoJS.enc.Base64.parse('VHpwNv+2xcLplx+a6AFdVw==');
    const ivCode = CryptoJS.enc.Base64.parse('D28TLjXCzfkFRpzqqEtzzA==');

    const encrypted = CryptoJS.AES.encrypt(CryptoJS.enc.Utf16LE.parse(password), keyCode, {
      iv: ivCode,
      mode: CryptoJS.mode.CBC,
      padding: CryptoJS.pad.Pkcs7
    });

    return encrypted.toString();
  }

  changePassword(currentPassword: string, newPassword: string, confirmPassword: string,
    newQuestion: string, newAnswer: string, operationId: number) {
    return this.httpClient.post<any>(
      `${this.config.BackendUri}/api/users/change-password`, {
      currentPassword,
      newPassword,
      confirmPassword,
      newQuestion,
      newAnswer,
      operationId
    });
  }

  checkChangePassword(userName: string, operationId: number): Observable<any> {
    return this.httpClient.get<any>(`${this.config.BackendUri}/api/users/${userName}/operation/${operationId}/check-change-password`);
  }

  changeOperation(operationId: number) {

    return this.httpClient.post<any>(`${this.config.BackendUri}/api/logins/user-logout/${operationId}`, null)
      .pipe(
        map(result => {
          this.config.JWT.OperationID = 0;
          this.config.JWT.OperationName = '';
          return true;
        }),
        catchError(this.handleError));
  }

  changeReason(reason: Reason) {
    this.httpClient.put<any>(
      `${this.config.BackendUri}/api/users/change-reason`, reason
    ).subscribe();
  }

  getUserTimeout() {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/users/${this.config.JWT.UserId}/timeout`
    ).pipe(catchError(this.handleError));
  }

  getRelationPermissions(userId: number) {
    return this.httpClient.get<any>(
      `${this.config.BackendUri}/api/users/${userId}/relation-permissions`
    ).pipe(catchError(this.handleError));
  }
}
